import './style.scss';
import React from 'react';
import { useHistory } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import NoticeBox from '../notice-box';
import { AuthService } from '../../../services';
import {
  setAuthenticated,
  setSimpleUserInfo,
  setAlarmList,
} from '../../../store/slices/globalSlice';
import Constants from '../../../constants';

const { DEFAULT_PROJECT_KEY } = Constants.storage;

const UserInfoBox = props => {
  const history = useHistory();
  const dispatch = useDispatch();
  const userInfo = useSelector(({ global }) => global.simpleUserInfo);

  const handleSignOut = async e => {
    e.preventDefault();
    await AuthService.signOut();
    dispatch(setAuthenticated(false));
    dispatch(
      setSimpleUserInfo({
        id: '',
        email: '',
        name: '',
        rank: '',
        username: '',
        donor: {},
        isAgreeTerms: false,
      }),
    );
    dispatch(setAlarmList([]));
    sessionStorage.removeItem(DEFAULT_PROJECT_KEY);
    history.replace('/intro');
  };

  const moveToMyPage = () => {
    history.push('/mypage/verify');
  };

  return (
    <div className='lnb'>
      <ul className='h-full w-full flex items-center justify-end'>
        <li className='ml-8'>
          <button
            title='마이페이지로 가기'
            className={`btn-mypage ${props.pageId === 'mypage' ? 'font-bold' : ''}`}
            onClick={moveToMyPage}
          >
            <i />
            <span>{`${userInfo.name || ''} ${userInfo.rank || ''}`}</span>
          </button>
        </li>
        <li className='ml-8'>
          <button type='button' onClick={handleSignOut}>
            <span>로그아웃</span>
          </button>
        </li>
        <li className='ml-8'>
          <NoticeBox />
        </li>
      </ul>
    </div>
  );
};

UserInfoBox.propTypes = {
  pageId: PropTypes.string,
};

export default UserInfoBox;
