import './styles/common.css';
import 'suneditor/dist/css/suneditor.min.css';
import React from 'react';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store';
import { AppInitializer } from './components/container';
import {
  AlarmPage,
  CommunicationCreatePage,
  CommunicationDetailPage,
  CommunicationEditPage,
  CommunicationListPage,
  DonationPage,
  IntroPage,
  MyPage,
  ReportPage,
  SummaryPage,
  TermsOfEmailSecurityPage,
  TermsOfPrivacyPage,
  TermsOfServicePage,
  TermsOfConditionsPage,
  VerifyPwPage,
} from './pages';

const App = () => {
  return (
    <Provider store={store}>
      <BrowserRouter>
        <AppInitializer>
          <Switch>
            <Route exact path='/'>
              <Redirect
                to={{
                  pathname: '/intro',
                }}
              />
            </Route>
            <Route path='/intro'>
              <IntroPage />
            </Route>
            <Route path='/summary'>
              <SummaryPage />
            </Route>
            <Route path='/donation'>
              <DonationPage />
            </Route>
            <Route path='/report'>
              <ReportPage />
            </Route>
            <Route exact path='/communication'>
              <Redirect
                to={{
                  pathname: '/communication/list',
                }}
              />
            </Route>
            <Route path='/communication/list'>
              <CommunicationListPage />
            </Route>
            <Route path='/communication/create'>
              <CommunicationCreatePage />
            </Route>
            <Route path='/communication/edit/:id'>
              <CommunicationEditPage />
            </Route>
            <Route path='/communication/post/:id'>
              <CommunicationDetailPage />
            </Route>
            <Route path='/alarm'>
              <AlarmPage />
            </Route>
            <Route exact path='/mypage'>
              <MyPage />
            </Route>
            <Route path='/mypage/verify'>
              <VerifyPwPage />
            </Route>
            <Route path='/terms/conditions'>
              <TermsOfConditionsPage />
            </Route>
            <Route path='/terms/email-security'>
              <TermsOfEmailSecurityPage />
            </Route>
            <Route path='/terms/privacy'>
              <TermsOfPrivacyPage />
            </Route>
            <Route path='/terms/service'>
              <TermsOfServicePage />
            </Route>
          </Switch>
        </AppInitializer>
      </BrowserRouter>
    </Provider>
  );
};

export default App;
