import './style.scss';
import React from 'react';
import PropTypes from 'prop-types';
import shortid from 'shortid';
import { NavLink } from 'react-router-dom';

const navigationConfig = [
  {
    id: 'summary',
    title: '프로젝트 개요',
    path: '/summary',
  },
  {
    id: 'donation',
    title: '기부 상세내역',
    path: '/donation',
  },
  {
    id: 'report',
    title: '프로젝트 리포트',
    path: '/report',
  },
  {
    id: 'communication',
    title: '커뮤니케이션',
    path: '/communication/list',
  },
];

const Navigation = props => {
  return (
    <div className='nav-container' style={props.style}>
      <nav>
        <ul role='menu' className='h-full flex items-center justify-around'>
          {(navigationConfig || []).map(it => (
            <li
              role='menuitem'
              key={shortid.generate()}
              className={`h-full flex items-center justify-center ${
                props.pageId === it.id ? 'selcted-line' : ''
              }`}
            >
              <NavLink to={it.path} activeClassName='selected-text'>
                {it.title}
              </NavLink>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
};

Navigation.propTypes = {
  pageId: PropTypes.string,
};

export default Navigation;
