import './style.scss';
import React, { useState, useEffect } from 'react';
import { useHistory, useLocation, Link } from 'react-router-dom';
import qs from 'querystring';
import { useDispatch, useSelector } from 'react-redux';
import HttpStatus from 'http-status';
import { Navigation, Header, CommonTable, ProjectTitle } from '../../components/container';
import { Layout } from '../../components/presentational';
import { StringUtil } from '../../utils';
import { ProjectService } from '../../services';
import { setAuthenticated } from '../../store/slices/globalSlice';

const LENGTH_OF_TABLE = 10;

const CommunicationListPage = () => {
  const history = useHistory();
  const location = useLocation();
  const dispatch = useDispatch();
  // const defaultProject = useSelector(({ project }) => project.defaultProject);
  // const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);
  const { defaultProject, simpleUserInfo } = useSelector(({ global, project }) => ({
    defaultProject: project.defaultProject,
    simpleUserInfo: global.simpleUserInfo,
  }));

  const [key, setKey] = useState('title');
  const [word, setWord] = useState('');
  const [start, setStart] = useState(1);
  const [total, setTotal] = useState(0);
  const [list, setList] = useState([]);
  const [conditions, setConditions] = useState({
    key: 'title',
    word: '',
    start: 1,
    length: LENGTH_OF_TABLE,
  });

  const moveToPostPage = data => {
    history.push(`/communication/post/${data.id}`);
  };

  const moveToCreateCommunicationPage = e => {
    e.preventDefault();
    history.push('/communication/create');
  };

  const getCommunicationList = async () => {
    try {
      const data = {
        start,
        length: LENGTH_OF_TABLE,
        key,
        word,
      };

      const response = await ProjectService.getCommunicationList(defaultProject.id, data);
      setTotal(response.count || 0);
      setList(response.list || []);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const column = [
    {
      id: 'title',
      label: '제목',
      width: '520px',
      classNames: 'text-left truncate',
      link: '/communication/post',
    },
    { id: 'regCompany', label: '소속', width: '200px', classNames: 'truncate' },
    {
      id: 'regName',
      label: '작성자',
      width: '180px',
      classNames: 'truncate',
      cellRenderer: row => {
        let str = row.regName || '';

        if (row.regRank) {
          str += ` ${row.regRank}`;
        }
        return str;
      },
    },
    {
      id: 'createDt',
      label: '작성일',
      width: '180px',
      classNames: 'truncate',
      cellRenderer: row => StringUtil.formatter.date(row.createDt, 'YYYY-MM-DD'),
    },
  ];

  const onChangeSearchKey = e => setKey(e.target.value);
  const onChangeSearchWord = e => setWord(e.target.value);
  const handleChangePage = page => {
    const conditions = {
      start: page + 1,
      key,
      word,
    };
    history.push(`/communication/list?${qs.stringify(conditions)}`);
  };

  const handleSearch = async e => {
    e.preventDefault();
    const conditions = {
      start: 1,
      key,
      word,
    };
    history.push(`/communication/list?${qs.stringify(conditions)}`);
  };

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      getCommunicationList();
    } else {
      history.replace('/terms/conditions');
    }
  }, [defaultProject]);

  // 파라미터 변경감지
  useEffect(() => {
    const { key, word, start } = qs.parse(location.search.slice(1));

    setKey(key || 'title');
    setWord(word || '');
    setStart((start - 1) * LENGTH_OF_TABLE || 0);
    setConditions({
      key: key || 'title',
      word: word || '',
      start: (start - 1) * LENGTH_OF_TABLE || 0,
      length: LENGTH_OF_TABLE,
    });
  }, [location.search]);

  // 재검색
  useEffect(() => {
    getCommunicationList();
  }, [conditions]);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '커뮤니케이션 목록 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header />}
      projectTitle={<ProjectTitle />}
      navigation={<Navigation pageId='communication' />}
      content={
        <>
          <div className='communication-container'>
            <div className='flex justify-between items-center mb-7'>
              <span className='total-text'>
                총 <b>{total}</b> 개 게시물
              </span>
              <div>
                <form noValidate onSubmit={handleSearch}>
                  <fieldset className='flex items-center'>
                    <legend className='accessible-hidden'>게시물 검색</legend>
                    <select
                      id='key'
                      name='key'
                      onChange={onChangeSearchKey}
                      value={key}
                      className='basic-select mr-2'
                      title='검색유형 선택'
                    >
                      <option value='title'>제목</option>
                      <option value='regCompany'>소속</option>
                      <option value='regName'>작성자</option>
                    </select>
                    <input
                      id='word'
                      name='word'
                      placeholder='검색어를 입력하세요.'
                      value={word}
                      onChange={onChangeSearchWord}
                      className='basic-input-text'
                      title='검색어를 입력하세요.'
                    />
                    <input type='submit' value='검색' className='basic-input-button' />
                  </fieldset>
                </form>
              </div>
            </div>
            <div>
              <CommonTable
                title='커뮤니케이션 게시판'
                tableClassName='table-fixed'
                summary='커뮤니케이션 게시판 no, 제목, 소속, 작성자 작성일 목록'
                emptyText='커뮤니케이션 내역이 없습니다.'
                column={column}
                data={list}
                onClickRow={moveToPostPage}
                usePagination
                total={total}
                currentPage={start}
                onChangePage={handleChangePage}
                useNo={true}
              />
            </div>
            {simpleUserInfo.username !== 'webwatch' && (
              <div className='text-right mb-20'>
                <Link className='orange-button-filled' to='/communication/create'>
                  <span>등록</span>
                </Link>
              </div>
            )}
          </div>
        </>
      }
    />
  );
};

export default CommunicationListPage;
