import './style.scss';
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';
import shortid from 'shortid';
import { useLocation, useHistory } from 'react-router-dom';
import HttpStatus from 'http-status';
import Constants from '../../constants';
import {
  Navigation,
  Header,
  ProjectTitle,
  CommentContainer,
  DownloadPdfButton,
} from '../../components/container';
import { Layout, Report } from '../../components/presentational';
import { FileUtil, StringUtil } from '../../utils';
import { ProjectService } from '../../services';
import { setAuthenticated } from '../../store/slices/globalSlice';
import FolderImage from '../../assets/images/background/folder.png';
import environments from '../../environments';
import { setDefaultProject } from '../../store/slices/projectSlice';

const ReportPage = () => {
  const dispatch = useDispatch();

  const history = useHistory();
  const location = useLocation();

  const reportRef = useRef();
  const { defaultProject, projectList } = useSelector(({ project }) => ({
    defaultProject: project.defaultProject,
    projectList: project.projectList,
  }));
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);

  const [releaseFlag, setReleaseFlag] = useState(false);
  const [reportList, setReportList] = useState([]);
  const [reportId, setReportId] = useState(null);
  const [reportInfo, setReportInfo] = useState({});
  const [reportStatus, setReportStatus] = useState({});
  const [reportCommentList, setReportCommentList] = useState([]);
  const [isIE, setIsIE] = useState(false);

  const getReportList = async () => {
    try {
      const { data } = await ProjectService.getReportListOfProject(defaultProject.id);
      const list = (data.list || [])
        .filter(item => item.isPublished && moment(item.publishDate).isSameOrBefore())
        .sort((a, b) => {
          const isSame = moment(a.publishDate).isSame(b.publishDate);

          if (isSame) {
            return 0;
          }
          return moment(a.publishDate).isBefore(b.publishDate) ? 1 : -1;
        });
      setReleaseFlag(data.releaseFlag);
      setReportList(list);

      if (location.state?.id) {
        setReportId(location.state.id);
      } else {
        setReportId(list.length > 0 ? list[0].id : null);
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const getReportInfo = async () => {
    try {
      const { data } = await ProjectService.getDetailInfoOfReport(reportId);
      setReportInfo({
        ...data,
        reportDataList: (data.reportDataList || []).sort((a, b) => {
          if (a.name < b.name) {
            return -1;
          }
          if (a.name > b.name) {
            return 1;
          }
          return 0;
        }),
      });
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      } else if (
        error.error_code &&
        (error.error_code === 'entity_not_found' || error.error_code === 'deleted_content')
      ) {
        alert('없거나 삭제된 리포트입니다.\n개요 페이지로 이동합니다.');
        history.replace('/summary');
      }
    }
  };

  const getReportStatus = async () => {
    try {
      const conditions = {
        sd: reportInfo.standardStartDate,
        ed: reportInfo.standardEndDate,
      };

      const { data } = await ProjectService.getStatusOfReport(
        reportInfo.projectDonor.id,
        conditions,
      );

      setReportStatus(data);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const getCommentListOfReport = async () => {
    try {
      const { data } = await ProjectService.getCommentListOfReport(reportId);
      setReportCommentList(data.list);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const handleSubmitComment = async (reportId, content, parentId) => {
    try {
      const response = await ProjectService.createCommentOfReport(reportId, content, parentId);

      if (response) {
        getCommentListOfReport();
        return true;
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const handleEditComment = async (reportId, newContent, parentId) => {
    if (StringUtil.formatter.noSpace(newContent) === '') {
      alert(Constants.message.Al_01_12);
      return;
    }

    try {
      const response = await ProjectService.updateCommentOfReport(reportId, newContent, parentId);

      if (response.success_message === 'process success.') {
        getCommentListOfReport();
        return true;
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const handleDeleteComment = async (reportId, parentId) => {
    try {
      const response = await ProjectService.deleteCommentOfReport(reportId, parentId);

      if (response.success_message === 'process success.') {
        getCommentListOfReport();
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const getReportFilename = info => {
    return `${moment(info.publishDate).format('YYYYMMDD')}일자_리포트_${info.title}.pdf`;
  };

  const handleChangeReport = e => {
    setReportId(Number(e.target.value));
  };

  const handleDownloadFile = e => {
    e.preventDefault();
    FileUtil.downloadHandler(e.target.href, e.target.title);
  };

  useEffect(() => {
    setIsIE(window.MSCompatibleInfo != null);
  }, []);

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      if (defaultProject.id) {
        getReportList();
      }
    } else {
      history.replace('/terms/conditions');
    }
  }, [defaultProject]);

  useEffect(() => {
    if (reportId !== null && releaseFlag) {
      getReportInfo();
      getCommentListOfReport();
    }
  }, [reportId]);

  useEffect(() => {
    if (reportInfo.id && releaseFlag) {
      getReportStatus();
    }
  }, [reportInfo]);

  useEffect(() => {
    if (location.state) {
      if (location.state.projectId && defaultProject.id !== location.state.projectId) {
        const newDefaultProject = (projectList || []).find(p => p.id === location.state.projectId);

        if (newDefaultProject) {
          dispatch(setDefaultProject(newDefaultProject));
        }
      }
    }
  }, [location]);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '프로젝트 리포트 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header />}
      projectTitle={<ProjectTitle />}
      navigation={<Navigation pageId='report' />}
      content={
        (reportList || []).length === 0 || !releaseFlag ? (
          <div className='empty-report-container'>
            <div className='emtpy-report-container-inner'>
              <img src={FolderImage} alt='발행된 리포트가 없음 이미지' width={108} height={86} />
              <strong>아직 발행된 프로젝트 리포트가 없습니다.</strong>
            </div>
          </div>
        ) : (
          <>
            <div className='report-container' ref={reportRef}>
              <div className='report-container-inner'>
                <select
                  className='basic-select mt-10'
                  id='option'
                  name='option'
                  value={reportId || undefined}
                  onChange={handleChangeReport}
                  title='프로젝트 리포트 리스트'
                >
                  {reportList.map(report => (
                    <option key={shortid.generate()} value={report.id}>
                      {moment(report.publishDate).format('YYYY년 MM월 DD일자')} 리포트
                    </option>
                  ))}
                </select>

                <div className='report-doc-wrap'>
                  <Report
                    id='report-document'
                    project={defaultProject}
                    status={reportStatus}
                    data={reportInfo}
                  />
                  {/* 첨부파일 */}
                  <div className='attach-wrap'>
                    <h3>첨부파일</h3>
                    <div>
                      {(reportInfo.reportAttachList || []).map(attach => (
                        <a
                          key={shortid.generate()}
                          target='_blank'
                          rel='noreferrer'
                          href={`${environments.API_URL}upload/${attach.path}`}
                          title={attach.originFileName}
                          download={attach.originFileName}
                          className='btn-download'
                          onClick={handleDownloadFile}
                        >
                          {attach.originFileName}
                        </a>
                      ))}
                    </div>
                  </div>
                </div>
                <div className='text-right pt-5 pb-20'>
                  <DownloadPdfButton
                    label='PDF 다운로드'
                    targetId='report-document'
                    filename={getReportFilename(reportInfo)}
                    isIE={isIE}
                  />
                </div>
              </div>
            </div>
            <CommentContainer
              id={reportId}
              comments={reportCommentList}
              getComments={getCommentListOfReport}
              onSubmitComment={handleSubmitComment}
              onEditComment={handleEditComment}
              onDeleteCommnet={handleDeleteComment}
            />
          </>
        )
      }
    />
  );
};

export default ReportPage;
