import './style.scss';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import shortid from 'shortid';
import { useHistory } from 'react-router-dom';
import HttpStatus from 'http-status';
import { Contents, Navigation, Header } from '../../components/container';
import { Layout, AlarmListItem, AlarmEmpty } from '../../components/presentational';
import {
  setAlarmList as setStoreAlarmList,
  setAuthenticated,
} from '../../store/slices/globalSlice';
import { NotificationService } from '../../services';

const DEFAULT_SHOW_ROW_NUM = 10;

const AlarmPage = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);

  const [isLastPage, setIsLastPage] = useState(false);
  const [page, setPage] = useState(0);
  const [alarmList, setAlarmList] = useState([]);

  const getAlarmList = async (currentPage = 0) => {
    try {
      const conditions = {
        start: currentPage * DEFAULT_SHOW_ROW_NUM,
        length: DEFAULT_SHOW_ROW_NUM,
      };
      const { data } = await NotificationService.getNotificationList(conditions);

      if (data) {
        setAlarmList(alarmList.concat(data.list));

        if (data.list.length < DEFAULT_SHOW_ROW_NUM) {
          setIsLastPage(true);
        }
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const refreshAlarmList = async () => {
    const conditions = {
      start: 0,
      length: 5,
      isChecked: false,
    };
    const { data } = await NotificationService.getNotificationList(conditions);

    if (data) {
      dispatch(setStoreAlarmList(data.list || []));
    }
  };

  const handleClickMore = () => {
    setPage(page + 1);
    getAlarmList(page + 1);
  };

  const handleClickAlarm = async ({ id, notificationType, contentId, isChecked, project }) => {
    try {
      if (!isChecked) {
        await NotificationService.readAlarm(id);
        refreshAlarmList();
      }
    } finally {
      switch (notificationType) {
        case 'BOARD':
          history.push(`/communication/post/${contentId}`, {
            projectId: project.id,
          });
          break;
        case 'REPORT':
          history.push('/report', { id: contentId, projectId: project.id });
          break;
        default:
          break;
      }
    }
  };

  const renderAlarmList = (list = []) => (
    <>
      {alarmList.length === 0 ? (
        <AlarmEmpty />
      ) : (
        <ul>
          {list.map(alarm => (
            <AlarmListItem
              key={shortid.generate()}
              read={alarm.isChecked}
              message={alarm.content}
              createDt={alarm.createDt}
              onClick={() => handleClickAlarm(alarm)}
            />
          ))}
        </ul>
      )}
    </>
  );

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      getAlarmList();
    } else {
      history.replace('/terms/conditions');
    }
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '알림 목록 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header />}
      navigation={<Navigation style={{ marginTop: '80px', top: 0 }} />}
      content={
        <Contents title='알림 목록'>
          <div className='alarm-container'>
            {renderAlarmList(alarmList)}

            {alarmList.length > 0 && !isLastPage && (
              <button type='button' className='btn-more' onClick={handleClickMore}>
                <span>더보기</span>
              </button>
            )}
          </div>
        </Contents>
      }
    />
  );
};

export default AlarmPage;
