import './style.scss';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import HttpStatus from 'http-status';
import { setAuthenticated, setSimpleUserInfo } from '../../store/slices/globalSlice';
import { Contents, Header, Navigation } from '../../components/container';
import { Divider, Maybe, Layout } from '../../components/presentational';
import { StringUtil } from '../../utils';
import Constants from '../../constants';
import { AuthService } from '../../services';

const MyPage = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);
  const [username, setUsername] = useState('');
  const [emailId, setEmailId] = useState('');
  const [emailDomainText, setEmailDomainText] = useState('');
  const [emailDomain, setEmailDomain] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const [passwordCheck, setPasswordCheck] = useState(false);
  const [name, setName] = useState('');
  const [rank, setRank] = useState('');
  const [position, setPosition] = useState('');
  const [mobile1, setMobile1] = useState('');
  const [mobile2, setMobile2] = useState('');
  const [mobile3, setMobile3] = useState('');
  const [isEmailTextReadOnly, setIsEmailTextReadOnly] = useState(false);

  const getAccountInfo = async () => {
    try {
      const { data } = await AuthService.getAccountInfo();
      const [mId, mDomain] = data.email.split('@');

      setUsername(data.username);
      setName(data.name);
      setRank(data.rank);
      setPosition(data.position);
      setEmailId(mId);
      setEmailDomainText(mDomain);
      setMobile1(data.mobile.split('-')[0]);
      setMobile2(data.mobile.split('-')[1]);
      setMobile3(data.mobile.split('-')[2]);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const handleChangePassword = e => {
    const passwordValue = StringUtil.formatter.noSpace(e.target.value);
    setPassword(passwordValue);

    if (passwordValue !== '' && passwordConfirm !== '') {
      setPasswordCheck(passwordValue === passwordConfirm);
    }
  };

  const handleChangePasswordConfirm = e => {
    const passwordConfirmValue = StringUtil.formatter.noSpace(e.target.value);
    setPasswordConfirm(passwordConfirmValue);

    if (passwordConfirmValue !== '' && passwordConfirm !== '') {
      setPasswordCheck(passwordConfirmValue === password);
    }
  };

  const handleChangeName = e => {
    setName(StringUtil.formatter.noSpace(e.target.value));
  };

  const handleChangeRank = e => {
    setRank(e.target.value);
  };

  const handleChangePosition = e => {
    setPosition(e.target.value);
  };

  const handleChangeMobile1 = e => {
    setMobile1(StringUtil.formatter.onlyNumber(e.target.value));
  };

  const handleChangeMobile2 = e => {
    setMobile2(StringUtil.formatter.onlyNumber(e.target.value));
  };

  const handleChangeMobile3 = e => {
    setMobile3(StringUtil.formatter.onlyNumber(e.target.value));
  };

  const handleChangeEmailId = e => {
    setEmailId(StringUtil.formatter.noSpace(e.target.value));
  };

  const handleChangeEmailDomainText = e => {
    setEmailDomainText(StringUtil.formatter.noSpace(e.target.value));
  };

  const handleChangeEmailDomain = e => {
    setIsEmailTextReadOnly(!(e.target.value === ''));
    setEmailDomainText(StringUtil.formatter.noSpace(e.target.value));
    setEmailDomain(e.target.value);
  };

  const moveToSummaryPage = () => {
    history.push('/summary');
  };

  const validate = () => {
    let msg = '';

    if (password === '') {
      msg = Constants.message.Al_03_01;
      document.getElementById('password').focus();
    } else if (
      !Constants.regex.REGEX_PASSWORD_1.test(password) &&
      !Constants.regex.REGEX_PASSWORD_3.test(password)
    ) {
      msg = Constants.message.Al_02_01;
      document.getElementById('password').focus();
    } else if (password !== passwordConfirm) {
      msg = Constants.message.Al_02_02;
      document.getElementById('passwordConfirm').focus();
    } else if (name === '') {
      msg = `이름${Constants.message.Al_03_04}`;
      document.getElementById('name').focus();
    }

    if (msg) {
      alert(msg);
      return false;
    }
    return true;
  };

  const handleSubmit = async e => {
    e.preventDefault();

    const isConfirm = window.confirm(Constants.message.Al_03_02);

    if (!isConfirm) {
      return;
    }

    if (!validate()) {
      return;
    }

    try {
      const payload = {
        password,
        name,
        rank,
        position,
        mobile: mobile1 + mobile2 + mobile3,
        email: `${emailId}@${emailDomainText}`,
      };

      await AuthService.updateAccountInfo(payload);

      dispatch(
        setSimpleUserInfo({
          id: simpleUserInfo.id,
          email: payload.email,
          name: payload.name,
          rank: payload.rank,
          username: payload.username,
          donor: simpleUserInfo.donor,
          isAgreeTerms: simpleUserInfo.isAgreeTerms,
        }),
      );
      alert(Constants.message.Al_03_03);

      setPassword('');
      setPasswordConfirm('');
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      getAccountInfo();
    } else {
      history.replace('/terms/conditions');
    }
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '마이페이지 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header pageId='mypage' />}
      navigation={<Navigation style={{ marginTop: '80px', top: 0 }} />}
      content={
        <Contents title='마이 페이지'>
          <div className='mypage-container'>
            <form onSubmit={handleSubmit}>
              <div className='col1'>
                <h3>회원정보</h3>
              </div>
              <div className='col2'>
                <fieldset>
                  <legend className='accessible-hidden'>기본정보</legend>
                  <ul>
                    <li>
                      <span className='label'>기부사</span>
                      <span>{simpleUserInfo.donor.name}</span>
                    </li>
                    <li>
                      <span className='label'>아이디</span>
                      <span>{username}</span>
                    </li>
                  </ul>
                </fieldset>

                <Divider />

                <fieldset>
                  <legend className='accessible-hidden'>필수입력항목</legend>
                  <p className='required-text'>*필수 입력 항목</p>
                  <ul>
                    <li>
                      <label htmlFor='password' className='required label'>
                        <span className='accessible-hidden'>필수 입력 항목</span> 비밀번호
                      </label>
                      <div>
                        <input
                          type='password'
                          id='password'
                          name='password'
                          placeholder='비밀번호 입력'
                          value={password}
                          onChange={handleChangePassword}
                          maxLength='15'
                          autoComplete='on'
                          required
                        />
                      </div>
                      <p className='helper-txt'>영문+숫자 or 영문+숫자+특수문자 6~15자</p>
                    </li>
                    <li>
                      <label htmlFor='passwordConfirm' className='required label'>
                        <span className='accessible-hidden'>필수 입력 항목</span> 비밀번호 확인
                      </label>
                      <div>
                        <input
                          type='password'
                          id='passwordConfirm'
                          name='passwordConfirm'
                          placeholder='비밀번호 입력'
                          value={passwordConfirm}
                          onChange={handleChangePasswordConfirm}
                          maxLength='15'
                          autoComplete='on'
                          required
                        />
                      </div>
                      <Maybe test={!passwordCheck && password.length > 0}>
                        <p className='helper-txt error'>비밀번호가 일치하지 않습니다.</p>
                      </Maybe>
                    </li>
                    <li>
                      <label htmlFor='name' className='required label'>
                        <span className='accessible-hidden'>필수 입력 항목</span> 이름
                      </label>
                      <div>
                        <input
                          type='text'
                          id='name'
                          name='name'
                          placeholder='이름 입력'
                          value={name}
                          onChange={handleChangeName}
                          maxLength='10'
                          required
                        />
                      </div>
                    </li>
                  </ul>
                </fieldset>

                <Divider />

                <fieldset>
                  <legend className='accessible-hidden'>추가입력항목</legend>
                  <ul>
                    <li>
                      <div className='half'>
                        <label htmlFor='rank' className='required'>
                          직급
                        </label>
                        <div>
                          <input
                            type='text'
                            id='rank'
                            name='rank'
                            placeholder='직급 입력'
                            value={rank}
                            onChange={handleChangeRank}
                            maxLength='10'
                          />
                        </div>
                      </div>
                      <div className='half'>
                        <label htmlFor='position' className='required'>
                          직책(팀)
                        </label>
                        <div>
                          <input
                            type='text'
                            id='position'
                            name='position'
                            placeholder='직책(팀) 입력'
                            value={position}
                            onChange={handleChangePosition}
                            maxLength='20'
                          />
                        </div>
                      </div>
                    </li>
                    <li>
                      <label htmlFor='mobile1' className='required'>
                        연락처
                      </label>
                      <input
                        className='mobile'
                        type='text'
                        id='mobile1'
                        name='mobile1'
                        value={mobile1}
                        maxLength='3'
                        onChange={handleChangeMobile1}
                      />
                      <span className='hyphen'>-</span>
                      <input
                        className='mobile'
                        type='text'
                        id='mobile2'
                        name='mobile2'
                        value={mobile2}
                        maxLength='4'
                        onChange={handleChangeMobile2}
                        title='연락처 가운데'
                      />
                      <span className='hyphen'>-</span>
                      <input
                        className='mobile'
                        type='text'
                        id='mobile3'
                        name='mobile3'
                        value={mobile3}
                        maxLength='4'
                        onChange={handleChangeMobile3}
                        title='연락처 마지막'
                      />
                    </li>
                    <li>
                      <label htmlFor='emailId' className='required'>
                        이메일
                      </label>
                      <input
                        className='email'
                        type='text'
                        id='emailId'
                        name='emailId'
                        value={emailId}
                        onChange={handleChangeEmailId}
                      />
                      <span className='email'>@</span>
                      <input
                        className='email'
                        type='text'
                        id='emailDomainText'
                        name='emailDomainText'
                        disabled={isEmailTextReadOnly}
                        value={emailDomainText}
                        onChange={handleChangeEmailDomainText}
                        title='도메인 입력'
                      />
                      <select
                        className='email'
                        id='emailDomain'
                        name='emailDomain'
                        value={emailDomain}
                        onChange={handleChangeEmailDomain}
                        title='도매인 선택하기'
                      >
                        <option value=''>직접입력</option>
                        <option value='naver.com'>naver.com</option>
                        <option value='daum.net'>daum.net</option>
                        <option value='hotmail.com'>hotmail.com</option>
                        <option value='hanmail.net'>hanmail.net</option>
                        <option value='gmail.com'>gmail.com</option>
                      </select>
                    </li>
                  </ul>
                </fieldset>
              </div>

              <Divider />

              <div className='btn-row'>
                <button type='button' className='cancel' onClick={moveToSummaryPage}>
                  <span>취소</span>
                </button>
                <button className='submit' type='submit'>
                  <span>확인</span>
                </button>
              </div>
            </form>
          </div>
        </Contents>
      }
    />
  );
};

export default MyPage;
