/* eslint-disable no-return-assign */
import './style.scss';
import React, { useState, useRef } from 'react';
import PropTypes from 'prop-types';
import renderHTML from 'react-render-html';
import { MomnetUtil, StringUtil } from '../../../utils';
import { Maybe, SwitchCase } from '../../presentational';
import RecommentItem from '../recomment-item';
import Constants from '../../../constants';
import useDidMountEffect from '../../presentational/useDidMountEffect';

const CommentItem = ({ id, comment, onSubmitComment, onEditComment, onDeleteCommnet }) => {
  const [isModify, setIsModify] = useState(false);
  const [newContent, setnewContent] = useState(comment.content);
  const [recommentContent, setRecommentContent] = useState('');

  const buttonRef = useRef(null);
  const textareaRef = useRef(null);

  const handleChangeContent = e => setnewContent(e.target.value);
  const handleChangeReCommentContent = e => setRecommentContent(e.target.value);

  const handleFocus = () => {
    if (isModify) {
      setTimeout(() => {
        textareaRef?.current?.focus();
      }, 100);
    } else {
      setTimeout(() => {
        buttonRef?.current?.focus();
      }, 100);
    }
  };

  const handleModify = () => {
    setIsModify(!isModify);
  };

  const handleEditCancel = e => {
    e.preventDefault();
    handleModify();
    setnewContent(comment.content);
  };

  const handleEditComment = async e => {
    e.preventDefault();

    if (StringUtil.formatter.noSpace(newContent) === '') {
      alert(Constants.message.Al_01_12);
      return;
    }

    if (onEditComment) {
      if (onEditComment(comment.id, newContent)) {
        handleModify();
      }
    }
  };

  const handleDeleteComment = async e => {
    e.preventDefault();

    if (onDeleteCommnet) {
      const isConfirm = window.confirm(Constants.message.Al_01_08);
      if (isConfirm) {
        onDeleteCommnet(comment.id);
      }
    }
  };

  const handleSubmitRecomment = async e => {
    e.preventDefault();

    if (StringUtil.formatter.noSpace(recommentContent) === '') {
      alert(Constants.message.Al_01_13);
      return;
    }

    if (onSubmitComment) {
      if (onSubmitComment(id, recommentContent, comment.id)) {
        e.target.querySelector('textarea').value = '';
        setRecommentContent('');
      }
    }
  };

  useDidMountEffect(() => {
    handleFocus();
  }, [isModify]);

  return (
    <li className='single-comment-container'>
      <div className='flex'>
        <b className='comment-name'>
          {comment.regCompany} {comment.regName} {comment.regRank}
        </b>
        <span className='comment-date'>
          <span>{MomnetUtil.formatter.date(comment.createDt, 'YYYY-MM-DD')}</span>
          <span className='comment-time'>
            {MomnetUtil.formatter.date(comment.createDt, 'HH:mm:ss')}
          </span>
        </span>
        <Maybe test={comment.creatorFlag && !isModify}>
          <div className='ml-auto'>
            <button
              title='게시물 수정'
              className='gray-edit-outlined'
              onClick={handleModify}
              ref={buttonRef}
            >
              <span>수정</span>
            </button>
            <button
              title='게시물 삭제'
              className='gray-delete-outlined'
              onClick={handleDeleteComment}
            >
              <span>삭제</span>
            </button>
          </div>
        </Maybe>
      </div>
      <div className='py-2'>
        <SwitchCase
          tests={[
            {
              test: isModify,
              component: (
                <form noValidate onSubmit={e => handleEditComment(e)}>
                  <fieldset>
                    <legend className='accessible-hidden'>댓글 수정하기</legend>
                    <label className='accessible-hidden' htmlFor={`comment-${comment.id}`}>
                      댓글 수정하기
                    </label>
                    <textarea
                      id={`comment-${comment.id}`}
                      placeholder='댓글을 입력해주세요.'
                      onChange={handleChangeContent}
                      maxLength='500'
                      className='comment-modify-textarea'
                      value={newContent}
                      ref={textareaRef}
                    ></textarea>
                    <div className='py-5 text-right'>
                      <button
                        className='orange-button-outlined'
                        type='button'
                        onClick={handleEditCancel}
                      >
                        <span>취소</span>
                      </button>
                      <input type='submit' className='orange-button-filled ml-3' value='등록' />
                    </div>
                  </fieldset>
                </form>
              ),
            },
          ]}
          defaultComponent={
            <div>{renderHTML(StringUtil.formatter.lineBreak(comment.content))}</div>
          }
        />
      </div>
      <div className='all-comments-container'>
        <Maybe test={!!comment.children}>
          <ul className='px-5'>
            {(comment.children || [])
              .sort((a, b) => new Date(a.createDt) - new Date(b.createDt))
              .map(item => (
                <RecommentItem
                  key={item.id}
                  commentId={comment.id}
                  recomment={item}
                  onEditComment={onEditComment}
                  onDeleteCommnet={onDeleteCommnet}
                />
              ))}
          </ul>
        </Maybe>
        <form noValidate className='px-6 py-5' onSubmit={handleSubmitRecomment}>
          <fieldset>
            <legend className='accessible-hidden'>답글 달기</legend>
            <div className='flex'>
              <label className='accessible-hidden' htmlFor={`recomment-${comment.id}`}>
                답글 달기
              </label>
              <textarea
                id={`recomment-${comment.id}`}
                className='recomment-modify-textarea'
                placeholder='댓글을 입력해주세요.'
                onChange={handleChangeReCommentContent}
                maxLength='500'
                value={recommentContent}
              ></textarea>
              <input type='submit' value='등록' className='basic-input-button' />
            </div>
          </fieldset>
        </form>
      </div>
    </li>
  );
};

CommentItem.propTypes = {
  id: PropTypes.number.isRequired,
  comment: PropTypes.object,
  onSubmitComment: PropTypes.func.isRequired,
  onEditComment: PropTypes.func.isRequired,
  onDeleteCommnet: PropTypes.func.isRequired,
};

export default CommentItem;
