import './style.scss';
import React from 'react';
import PropTypes from 'prop-types';
import CountUp from 'react-countup';

const InfographicCard = ({
  order = 0,
  backgroundUrl,
  foregroundUrl,
  foregroundPosition = { top: 0, left: 0 },
  label,
  unit,
  value = 0,
  containerStyle = {},
}) => {
  const calcCardAnimationDelay = () => order * 0.3 + 0.3;

  const calcCountDelay = () => order * 0.3 + 1;

  const calcForegroundDelay = () => order * 0.3 + 0.6;

  return (
    <div
      className='infographic-card'
      style={{
        ...containerStyle,
        backgroundImage: `url(${backgroundUrl})`,
        animationDelay: calcCardAnimationDelay() + 's',
        WebkitAnimationDelay: calcCardAnimationDelay() + 's',
        OAnimationDelay: calcCardAnimationDelay() + 's',
        MozAnimationDelay: calcCardAnimationDelay() + 's',
      }}
    >
      <img
        src={foregroundUrl}
        style={{
          ...foregroundPosition,
          animationDelay: calcForegroundDelay() + 's',
          WebkitAnimationDelay: calcForegroundDelay() + 's',
          OAnimationDelay: calcForegroundDelay() + 's',
          MozAnimationDelay: calcForegroundDelay() + 's',
        }}
        alt=''
      />
      <h4>{label}</h4>
      <div className='value-row'>
        <span className='value'>
          <CountUp separator=',' delay={calcCountDelay()} start={0} end={value} duration={1} />
        </span>
        <span className='unit'>{unit}</span>
      </div>
    </div>
  );
};

InfographicCard.propTypes = {
  order: PropTypes.number,
  backgroundUrl: PropTypes.string.isRequired,
  foregroundUrl: PropTypes.string.isRequired,
  foregroundPosition: PropTypes.shape({
    top: PropTypes.number,
    left: PropTypes.number,
  }),
  label: PropTypes.string.isRequired,
  value: PropTypes.number.isRequired,
  unit: PropTypes.string.isRequired,
  containerStyle: PropTypes.any,
};

export default InfographicCard;
