import './style.scss';
import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import Footer from '../footer';

const Layout = props => {
  const authenticated = useSelector(({ global }) => global.authenticated);

  if (props.navigation) {
    useEffect(() => {
      if (authenticated) {
        const header = document.getElementById('navigation');
        const sticky = header.offsetTop;
        const scrollCallBack = window.addEventListener('scroll', () => {
          if (window.pageYOffset > sticky) {
            header.classList.add(props.projectTitle ? 'sticky-80' : 'sticky-0');
          } else {
            header.classList.remove(props.projectTitle ? 'sticky-80' : 'sticky-0');
          }
        });

        return () => {
          window.removeEventListener('scroll', scrollCallBack);
        };
      }
    }, []);
  }

  return (
    <div>
      <div className='skip-navigation'>
        <a href='#content'>본문 바로가기</a>
      </div>
      {props.header && (
        <div className='header-wrap' style={{ position: !authenticated ? 'static' : '' }}>
          {props.header}
        </div>
      )}
      {props.projectTitle && <div className='projectTitle-wrap'>{props.projectTitle}</div>}
      {props.navigation && authenticated && (
        <div id='navigation' className='navigation-wrap'>
          {props.navigation}
        </div>
      )}
      {props.content && <div id='content'>{props.content}</div>}
      <Footer />
    </div>
  );
};

Layout.propTypes = {
  header: PropTypes.node,
  projectTitle: PropTypes.node,
  navigation: PropTypes.node,
  content: PropTypes.node,
};

export default Layout;
