import './style.scss';
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Constants from '../../../constants';
import { StringUtil } from '../../../utils';

const CommentForm = ({ id, onSubmitComment }) => {
  const [content, setConent] = useState('');

  const handleChangeContent = e => setConent(e.target.value);

  const handleClearContent = e => {
    e.preventDefault();
    setConent('');
  };

  const handleSubmit = async e => {
    e.preventDefault();

    if (StringUtil.formatter.noSpace(content) === '') {
      alert(Constants.message.Al_01_12);
      return;
    }

    if (onSubmitComment) {
      if (onSubmitComment(id, content)) {
        setConent('');
      }
    }
  };

  return (
    <div className='comment-form'>
      <form noValidate onSubmit={handleSubmit}>
        <fieldset>
          <legend>댓글</legend>
          <textarea
            value={content}
            placeholder='댓글을 입력해주세요.'
            onChange={handleChangeContent}
            maxLength='500'
            title='댓글 입력'
          >
            {content}
          </textarea>
          <div className='flex justify-end pt-2 pb-10'>
            <button className='orange-button-outlined' onClick={handleClearContent}>
              <span> 취소</span>
            </button>
            <input type='submit' className='orange-button-filled ml-3' value='등록' />
          </div>
        </fieldset>
      </form>
    </div>
  );
};

CommentForm.propTypes = {
  id: PropTypes.number,
  onSubmitComment: PropTypes.func.isRequired,
};

export default CommentForm;
