import qs from 'querystring';
import RestService from '../RestService';

/**
 * 파일 업로드
 * @param files
 * @param directory
 */
const uploadFile = async (files, directory) => {
  const FILE_KEY = 'files';
  const url = '/files';
  const formData = new FormData();

  if (files instanceof Array) {
    files.forEach(file => formData.append(FILE_KEY, file));
  } else {
    formData.append(FILE_KEY, files);
  }

  formData.append('directory', directory);

  const requestConfig = {
    url,
    method: 'post',
    data: formData,
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  };

  const { list } = await RestService.request(requestConfig);
  return list;
};

/**
 * 파일 존재 여부
 * @param conditions
 */
const getIsFile = async conditions => {
  const url = `/files?${qs.stringify(conditions)}`;
  const { is } = await RestService.get(url);
  return is;
};

/**
 * 파일 삭제
 * @param payload
 */
const deleteFile = async payload => {
  const url = `/files`;
  const requestConfig = {
    url,
    method: 'delete',
    data: payload,
  };
  const response = await RestService.request(requestConfig);
  return response;
};

export default {
  uploadFile,
  getIsFile,
  deleteFile,
};
