import './style.scss';
import React, { useEffect, useState } from 'react';
import HttpStatus from 'http-status';
import renderHTML from 'react-render-html';
import shortid from 'shortid';
import ReactECharts from 'echarts-for-react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { setAuthenticated } from '../../store/slices/globalSlice';
import { Navigation, Header, ProjectTitle } from '../../components/container';
import { Layout, Maybe } from '../../components/presentational';
import { NumberUtil } from '../../utils';
import { ProjectService } from '../../services';

const HUNDRED_MILLION = 100000000; // 억
const ONE_MILLION = 1000000; // 백만
const TEN_MILLION = 100000; // 십만
const TEN_THOUSAND = 10000; // 만
const { commas } = NumberUtil.formatter;

const ProjectSummaryPage = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const defaultProject = useSelector(({ project }) => project.defaultProject);
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);

  const [summaryInfo, setSummaryInfo] = useState({});

  const [requiredBudgetData, setRequiredBudgetData] = useState({
    compList: [],
    compAmount: 0,
    ingAmount: 0,
    workAmount: 0,
    sum: 0,
  });

  const [donationPlanData, setDonationPlanData] = useState({});
  const [workingPlanData, setWorkingPlanData] = useState({});
  const [totalBudgetData, setTotalBudgetData] = useState({
    totalBudget: 0,
    totalUsed: 0,
    totalUsedRatio: 0,
  });

  const convertBudgetUnit = amount => {
    let num;
    let suffix;

    if (amount > HUNDRED_MILLION) {
      num = amount / HUNDRED_MILLION;
      suffix = '억';
    } else {
      num = (amount - (amount % TEN_THOUSAND)) / TEN_THOUSAND;
      suffix = '만';
    }

    const [integer, decimal] = parseFloat(num.toFixed(2)).toString().split('.');
    return commas(integer) + (decimal ? `.${decimal}` : '') + suffix;
  };

  const getOptions = () => {
    return {
      tooltip: {
        triggerOn: 'none',
        trigger: 'item',
        formatter: params => {
          const colorSpan = color =>
            `<span style="display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color:${color}"></span>`;
          return `<p>${colorSpan(params.color)} ${
            params.name
          }<span style="display:inline-block;margin-left:15px;font-weight:600;">${convertBudgetUnit(
            params.value,
          )}원</span></p>`;
        },
        textStyle: {
          fontFamily: 'Noto Sans KR',
        },
      },
      color: ['#e66526', '#7b7572', '#4d1c86'],
      series: [
        {
          type: 'pie',
          radius: ['70%', '80%'],
          label: {
            show: true,
          },
          itemStyle: {
            borderColor: '#fff',
            borderWidth: 1,
          },
          data: [
            {
              value: requiredBudgetData.compAmount,
              name: '모금완료',
            },
            {
              value: requiredBudgetData.ingAmount,
              name: '모금중',
            },
            {
              value: requiredBudgetData.workAmount,
              name: '사업비',
            },
          ],
        },
      ],
    };
  };

  // 소요예산 데이터 변환
  const convertRequiredBudgetData = (compList = [], ingAmount = 0, workAmount = 0) => {
    const data = {
      ingAmount,
      workAmount,
      ingList: compList,
      compAmount: compList.reduce((acc, item) => acc + (item.amount || 0), 0),
      sum: 0,
    };
    data.sum = data.ingAmount + data.workAmount + data.compAmount;
    return data;
  };

  // 사용률 계산
  const calcUsedRatio = (numerator, denominator, decimalLength) => {
    return parseFloat(((numerator / denominator) * 100).toFixed(decimalLength));
  };

  // 원 → 백만 단위 변환
  const convertToMillion = (value = 0, decimalLength = 0) => {
    return parseFloat((value / ONE_MILLION).toFixed(decimalLength));
  };

  const convertTableData = (rows = [], childrenKey) => {
    const convertedData = {
      rows: [],
      sum: {
        budgetAmount: 0,
        usedAmount: 0,
        usedRatio: 0,
      },
    };

    rows.forEach(or => {
      const convertChildren = or[childrenKey].map(ir => ({
        ...ir,
        budgetAmount: ir.budgetAmount,
        usedAmount: ir.usedAmount,
      }));

      convertedData.rows.push({
        ...or,
        children: convertChildren,
      });

      (convertChildren || []).forEach(ir => {
        convertedData.sum.budgetAmount += ir.budgetAmount || 0;
        convertedData.sum.usedAmount += ir.usedAmount || 0;
      });
    });

    convertedData.sum.usedRatio = calcUsedRatio(
      convertedData.sum.usedAmount,
      convertedData.sum.budgetAmount,
      2,
    );
    return convertedData;
  };

  const getProjectSummary = async () => {
    try {
      const { data } = await ProjectService.getProjectSummary(defaultProject.id);

      if (data) {
        // 소요예산 데이터 변환
        const rData = convertRequiredBudgetData(
          data.fundraisingCompletedAmountList,
          data.fundraisingAmount,
          data.workingExpenses,
        );

        // 예산계획 및 사용현황 - 기부금 데이터 변환
        const dData = convertTableData(data.donationPlanList, 'donationPlanDetailList');

        // 예산계획 및 사용현황 - 사업비 데이터 변환
        const wData = convertTableData(
          data.workingExpensesPlanList,
          'workingExpensesPlanDetailList',
        );

        // 예산계획 및 사용현황 - 예산 총계 데이터 변환
        const tData = {
          totalBudget: dData.sum.budgetAmount + wData.sum.budgetAmount,
          totalUsed: dData.sum.usedAmount + wData.sum.usedAmount,
          totalUsedRatio: 0,
        };
        tData.totalUsedRatio = calcUsedRatio(tData.totalUsed, tData.totalBudget, 2);

        setSummaryInfo(data);
        setRequiredBudgetData(rData);
        setDonationPlanData(dData);
        setWorkingPlanData(wData);
        setTotalBudgetData(tData);
      } else {
        setSummaryInfo({});
        setRequiredBudgetData({
          compList: [],
          compAmount: 0,
          ingAmount: 0,
          workAmount: 0,
          sum: 0,
        });
        setDonationPlanData({});
        setWorkingPlanData({});
        setTotalBudgetData({
          totalBudget: 0,
          totalUsed: 0,
          totalUsedRatio: 0,
        });
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const renderTable = ({ rows, sum }, captionText) => {
    if (!(rows && sum)) {
      return null;
    }
    const isHiddenDetailName =
      rows
        .reduce((acc, { children = [] }) => {
          return [...acc, ...children.map(detail => !!detail.name)];
        }, [])
        .filter(name => name).length === 0;

    return (
      <table className='summary-tbl'>
        <caption>
          {captionText} 구분, {!isHiddenDetailName && '세부항목,'} 예산총액, 사용총액, 사용률 목록
        </caption>
        <colgroup>
          <col style={{ width: `${isHiddenDetailName ? '60%' : '30%'}` }} />
          {!isHiddenDetailName ? <col style={{ width: '30%' }} /> : null}
          <col style={{ width: '15%' }} />
          <col style={{ width: '15%' }} />
          <col style={{ width: '15%' }} />
        </colgroup>
        <thead>
          <tr>
            <th scope='col'>구분</th>
            {!isHiddenDetailName ? <th scope='col'>세부항목</th> : null}
            <th scope='col' className='text-right'>
              예산총액
            </th>
            <th scope='col' className='text-right'>
              사용총액
            </th>
            <th scope='col' className='text-right pr-5'>
              사용률
            </th>
          </tr>
        </thead>
        <tfoot>
          <tr>
            <td className='bold' colSpan={!isHiddenDetailName ? 2 : 1}>
              소계
            </td>
            <td className='text-right orange'>{commas(convertToMillion(sum.budgetAmount, 1))}</td>
            <td className='text-right orange'>{commas(convertToMillion(sum.usedAmount, 1))}</td>
            <td className='text-right orange pr-5'>
              {calcUsedRatio(sum.usedAmount, sum.budgetAmount, 1)}%
            </td>
          </tr>
        </tfoot>
        <tbody>
          {rows.map(item =>
            (item.children || []).map((detail, colnum) => {
              const budgetAmount = detail.budgetAmount || 0;
              const usedAmount = detail.usedAmount || 0;
              const usedRatio = parseFloat(((usedAmount / budgetAmount || 0) * 100).toFixed(2));

              return (
                <tr key={shortid.generate()}>
                  {colnum === 0 && (
                    <td
                      className={`bold ${item.children.length > 1 ? 'border-right' : ''}`}
                      rowSpan={item.children.length}
                    >
                      {item.name}
                    </td>
                  )}
                  {!isHiddenDetailName ? <td>{detail.name}</td> : null}
                  <td className='text-right'>{commas(convertToMillion(budgetAmount, 1))}</td>
                  <td className='text-right'>{commas(convertToMillion(usedAmount, 1))}</td>
                  <td className='text-right pr-5'>{parseFloat(usedRatio.toFixed(1))}%</td>
                </tr>
              );
            }),
          )}
        </tbody>
      </table>
    );
  };

  const resizeIframe = obj => {
    obj.style.height = obj.contentWindow.document.documentElement.scrollHeight + 'px';
  };

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      getProjectSummary();
    } else {
      history.replace('/terms/conditions');
    }
  }, [defaultProject]);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '프로젝트 개요 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header />}
      projectTitle={<ProjectTitle />}
      navigation={<Navigation pageId='summary' />}
      content={
        <div className='summary-container'>
          <p className='summary-container--quater'>* {summaryInfo.quarter || ''}</p>
          <ul>
            <li className='summary-container--list-item'>
              <h4>사회문제</h4>
              <div>{renderHTML(summaryInfo.question || '')}</div>
            </li>

            <li className='summary-container--list-item'>
              <h4>해결방법</h4>
              <div>{renderHTML(summaryInfo.answer || '')}</div>
            </li>

            <li className='summary-container--list-item'>
              <h4>진행현황</h4>
              <div>{renderHTML(summaryInfo.progressStatus || '')}</div>
            </li>

            <li className='summary-container--list-item'>
              <h4>소요예산</h4>
              <div>
                {/* 소요예산 차트 영역 */}
                <div className='graph-container'>
                  <div className='graph-container--meta'>
                    <h6 className='graph-container--meta--title'>총액</h6>
                    <span className='graph-container--meta--budget'>
                      {convertBudgetUnit(requiredBudgetData.sum)}원
                    </span>
                  </div>
                  <ReactECharts option={getOptions()} />
                  <div className='accessible-hidden'>
                    총액 {convertBudgetUnit(requiredBudgetData.sum)}원
                    <dl>
                      <dt>기부금</dt>
                      <dd>
                        모금완료 {convertBudgetUnit(requiredBudgetData.compAmount)}원 <br />
                        모금중 {convertBudgetUnit(requiredBudgetData.ingAmount)}원
                      </dd>
                      <dt>사업비</dt>
                      <dd>{convertBudgetUnit(requiredBudgetData.workAmount)}원</dd>
                    </dl>
                  </div>
                </div>

                {/* 소요예산 정보 */}
                <div className='budget-container'>
                  <dl>
                    <dt>
                      <b>기부금</b>
                    </dt>
                    <dd>
                      <div className='mb-2 mt-4'>
                        <div className='budget-container--status-flag color-1' />
                        <span className='orange-title font-bold mx-2'>모금완료</span>
                        <b>{convertBudgetUnit(requiredBudgetData.compAmount)}원</b>
                      </div>
                      <p>
                        {(summaryInfo.fundraisingCompletedAmountList || []).map(item => (
                          <span key={shortid.generate()} className='gray-tag'>
                            {item.name}
                            {/* 
                              @author deokchanjung
                              @date 2021.07.15
                              @description
                              고객사 요청에 의해 금액 히든처리 
                            */}
                            {/* {`${item.name} ${convertBudgetUnit(item.amount || 0)}원`} */}
                          </span>
                        ))}
                      </p>
                    </dd>
                    <dd>
                      <div className='budget-container--status-flag color-2' />
                      <span className='orange-title font-bold mx-2'>모금중</span>
                      &nbsp;
                      <b>{convertBudgetUnit(requiredBudgetData.ingAmount)}원</b>
                    </dd>
                  </dl>
                  <hr />
                  <dl>
                    <dt>
                      <b>사업비</b>
                    </dt>
                    <dd>
                      <div className='budget-container--status-flag color-3' />
                      <span className='gray-title font-bold mx-2'>행복나눔재단</span>
                      &nbsp;
                      <b>{convertBudgetUnit(requiredBudgetData.workAmount)}원</b>
                    </dd>
                  </dl>
                </div>
              </div>
            </li>
            <li className='summary-container--list-item'>
              <h4>
                예산계획 및<br />
                사용현황
              </h4>
              <dl>
                {/* 기부금 정보 */}
                <dt className='flex items-center'>
                  <b>기부금</b>
                  <span className='gray-title font-normal ml-2'>솔루션 제공</span>
                  <i className='gray-caption'>단위: 백만원</i>
                </dt>
                <dd>{renderTable(donationPlanData, '기부금')}</dd>

                {/* 사업비 정보 */}
                <dt className='flex items-center'>
                  <b>사업비</b>
                  <span className='gray-title font-normal ml-2'>운영지원</span>
                  <i className='gray-caption'>단위: 백만원</i>
                </dt>
                <dd>{renderTable(workingPlanData, '사업비')}</dd>

                {/* 예산 총계 정보 */}
                <dt>
                  <b>예산 총계</b>
                  <i className='gray-caption'>단위: 백만원</i>
                </dt>
                <dd>
                  <div className='total-container'>
                    <p>
                      <b className='gray-title font-bold'>예산 총액</b>
                      <br />
                      <span className='orange-title font-bold'>
                        {commas(convertToMillion(totalBudgetData.totalBudget, 1))}
                      </span>
                    </p>
                    <p>
                      <b className='gray-title font-bold'>사용 총액</b>
                      <br />
                      <span className='orange-title font-bold'>
                        {commas(convertToMillion(totalBudgetData.totalUsed, 1))}
                      </span>
                    </p>
                    <p>
                      <b className='gray-title font-bold'>총 사용률</b>
                      <br />
                      <span className='orange-title font-bold'>
                        {parseFloat((totalBudgetData.totalUsedRatio || 0).toFixed(1))}%
                      </span>
                    </p>
                  </div>
                </dd>
              </dl>
            </li>
          </ul>
          <div>
            <Maybe test={(summaryInfo.staticFilePath || '').includes('/htmls/projects/project')}>
              <iframe
                src={summaryInfo.staticFilePath}
                frameBorder='0'
                scrolling='no'
                onLoad={e => resizeIframe(e.target)}
              />
            </Maybe>
          </div>
        </div>
      }
    />
  );
};

export default ProjectSummaryPage;
