/* eslint-disable no-return-assign */
/* eslint-disable no-use-before-define */
import './style.scss';
import React, { useState, useEffect, useRef } from 'react';
import HttpStatus from 'http-status';
import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';
import ExpandLessRoundedIcon from '@material-ui/icons/ExpandLessRounded';
import shortid from 'shortid';
import qs from 'querystring';
import { useHistory, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { setAuthenticated } from '../../store/slices/globalSlice';
import {
  Navigation,
  Header,
  ProjectTitle,
  CommonTable,
  BeneficiaryDialog,
} from '../../components/container';
import { InfographicCard, Layout, Maybe } from '../../components/presentational';
import { NumberUtil, MomnetUtil } from '../../utils';
import { ProjectService } from '../../services';
import bgInfographic1 from '../../assets/images/background/bg-infographic-1.svg';
import fgInfographic1 from '../../assets/images/background/fg-infographic-1.svg';
import bgInfographic2 from '../../assets/images/background/bg-infographic-2.svg';
import fgInfographic2 from '../../assets/images/background/fg-infographic-2.svg';
import bgInfographic3 from '../../assets/images/background/bg-infographic-3.svg';
import fgInfographic3 from '../../assets/images/background/fg-infographic-3.svg';

const LENGTH_OF_TABLE = 10;
const HIDDEN_FROM_NUM = 10;

const DonationPage = () => {
  const dispatch = useDispatch();
  const defaultProject = useSelector(({ project }) => project.defaultProject);
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);
  const history = useHistory();
  const location = useLocation();

  const itemsRef = useRef([]); // 포커스를 위한 array

  const [releaseFlag, setReleaseFlag] = useState(false);
  const [summaryInfo, setSummaryInfo] = useState({
    accumulatedDonation: 0,
    usedDonation: 0,
    remainingDonation: 0,
    beneficiaryCount: 0,
  });
  const [donationStatus, setDonationStatus] = useState([]);
  const [donationList, setDonationList] = useState([]);
  const [matchingList, setMatchingList] = useState([]);
  const [beneficiaryList, setBeneficiaryList] = useState([]);
  const [hiddenfrom, setHiddenfrom] = useState(HIDDEN_FROM_NUM);
  const [beneficiaryListTotal, setBeneficiaryListTotal] = useState(0);
  const [start, setStart] = useState(1);
  const [conditions, setConditions] = useState({
    start: 1,
    length: LENGTH_OF_TABLE,
  });
  const [beneficiaryId, setBeneficiaryId] = useState('');
  const [isShowBeneficiaryDialog, SetIsShowBeneficiaryDialog] = useState(false);
  const [useColumnFlag, setUseColumnFlag] = useState({
    useBirthYear: false,
    useGender: false,
    useLocation: false,
    useMainCategory: false,
    useMiddleCategory: false,
    useName: false,
    useRegistrationNumber: false,
    useRemark: false,
    useSubCategory: false,
  });

  const setInitialState = () => {
    setSummaryInfo({
      accumulatedDonation: 0,
      usedDonation: 0,
      remainingDonation: 0,
      beneficiaryCount: 0,
    });
    setDonationStatus([]);
    setDonationList([]);
    setMatchingList([]);
    setBeneficiaryListTotal(0);
    setBeneficiaryList([]);
  };

  const maskName = (name = '') => {
    let maskedName = '*';
    if (!name) {
      maskedName = '-';
    } else if (name.length < 3) {
      maskedName = name.substring(0, 1) + '*';
    } else if (name.length >= 3) {
      maskedName = name.substring(0, 2) + '*';
    }
    return maskedName;
  };

  const getDonationInfoByBeneficiary = async () => {
    try {
      const { data } = await ProjectService.getDonationInfoByBeneficiary(
        defaultProject.id,
        conditions,
      );
      setBeneficiaryListTotal(data.count || 0);
      setBeneficiaryList(data.list || []);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const getDetailInfoOfProject = async () => {
    try {
      const { project } = await ProjectService.getDetailInfoOfProjectForBeneficiary(
        defaultProject.id,
      );

      setUseColumnFlag({
        useRegistrationNumber: project.useRegistrationNumber,
        useName: project.useName,
        useGender: project.useGender,
        useBirthYear: project.useBirthYear,
        useLocation: project.useLocation,
        useMainCategory: project.useMainCategory,
        useMiddleCategory: project.useMiddleCategory,
        useSubCategory: project.useSubCategory,
      });
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const getDonationInfoBySummary = async () => {
    try {
      setInitialState();

      const { data } = await ProjectService.getDonationInfoBySummary(defaultProject.id);
      setReleaseFlag(data.releaseFlag);
      if (data.releaseFlag) {
        await getDetailInfoOfProject();
        await getDonationInfoByBeneficiary();

        setSummaryInfo(
          Object.assign(summaryInfo, {
            accumulatedDonation: data.accumulatedDonation,
            usedDonation: data.usedDonation,
            remainingDonation: data.remainingDonation,
            beneficiaryCount: data.beneficiaryCount,
          }),
        );
        setDonationStatus([
          {
            accumulatedDonation: data.accumulatedDonation,
            usedDonation: data.usedDonation,
            remainingDonation: data.remainingDonation,
          },
        ]);
        setDonationList(data.donationList || []);
        setMatchingList(
          (data.matchingList || []).sort((a, b) => {
            if (a.name < b.name) {
              return -1;
            }
            if (a.name > b.name) {
              return 1;
            }
            return 0;
          }),
        );
      } else {
        setInitialState();
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      getDonationInfoBySummary();
    } else {
      history.replace('/terms/conditions');
    }
  }, [defaultProject]);

  // 파라미터 변경감지
  useEffect(() => {
    const { start } = qs.parse(location.search.slice(1));

    setStart((start - 1) * LENGTH_OF_TABLE || 0);
    setConditions({
      start: (start - 1) * LENGTH_OF_TABLE || 0,
      length: LENGTH_OF_TABLE,
    });
  }, [location.search]);

  // 재검색
  useEffect(() => {
    if (releaseFlag) {
      getDonationInfoByBeneficiary();
    }
  }, [conditions]);

  const handleOpenBeneficiaryDialog = beneficiaryId => {
    setBeneficiaryId(beneficiaryId);
    SetIsShowBeneficiaryDialog(true);
  };

  const handleFocus = id => {
    setTimeout(() => {
      itemsRef.current[id].focus();
    }, 100);
  };

  const handleCloseBeneficiaryDialog = () => {
    if (itemsRef && itemsRef.current[beneficiaryId]) {
      handleFocus(beneficiaryId);
    }

    setBeneficiaryId('');
    SetIsShowBeneficiaryDialog(false);
  };

  const handleChangePage = page => {
    const conditions = {
      start: page + 1,
    };
    history.push(`/donation?${qs.stringify(conditions)}`);
  };

  const handleChangeHiddenfrom = () => {
    setHiddenfrom(hiddenfrom === HIDDEN_FROM_NUM ? matchingList.length : HIDDEN_FROM_NUM);
  };

  const donationStatusColumn = [
    {
      id: 'accumulatedDonation',
      label: '누적 기부금',
      suffix: '원',
      cellRenderer: row => NumberUtil.formatter.commas(row.accumulatedDonation),
    },
    {
      id: 'usedDonation',
      label: '누적 사용금',
      suffix: '원',
      cellRenderer: row => NumberUtil.formatter.commas(row.usedDonation),
    },
    {
      id: 'remainingDonation',
      label: '잔여 기부금',
      suffix: '원',
      cellRenderer: row => NumberUtil.formatter.commas(row.remainingDonation),
    },
  ];

  const getColumn = () => {
    const column = [];

    if (!isShowBeneficiaryDialog && useColumnFlag.useRegistrationNumber) {
      column.push({
        id: 'registrationNumber',
        label: '대상자 번호',
      });
    }
    if (!isShowBeneficiaryDialog && useColumnFlag.useName) {
      column.push({
        id: 'name',
        label: '이름',
        cellRenderer: row => maskName(row.name),
      });
    }
    if (useColumnFlag.useGender) {
      column.push({
        id: 'gender',
        label: '성별',
        cellRenderer: row => {
          if (row.gender === 'FEMALE') {
            return '여';
          }
          if (row.gender === 'MALE') {
            return '남';
          }
          return '';
        },
      });
    }
    if (useColumnFlag.useBirthYear) {
      column.push({
        id: 'birthYear',
        label: '생년',
      });
    }
    if (useColumnFlag.useLocation) {
      column.push({
        id: 'location',
        label: '지역',
      });
    }
    if (useColumnFlag.useMainCategory) {
      column.push({
        id: 'mainCategory',
        label: '대분류',
      });
    }
    if (useColumnFlag.useMiddleCategory) {
      column.push({
        id: 'middleCategory',
        label: '중분류',
      });
    }
    if (useColumnFlag.useSubCategory) {
      column.push({
        id: 'subCategory',
        label: '소분류',
      });
    }

    column.push(
      {
        id: 'totalAmount',
        label: '제공 총액',
        suffix: '원',
        classNames: 'text-right',
        cellRenderer: row => NumberUtil.formatter.commas(row.totalAmount),
      },
      {
        id: 'detail',
        label: '제공 내역',
        // eslint-disable-next-line react/display-name
        cellRenderer: row => (
          <button
            className='registration-popup-button'
            ref={el => (itemsRef.current[row.id] = el)}
            onClick={() => handleOpenBeneficiaryDialog(row.id.toString())}
          >
            보기
          </button>
        ),
      },
    );
    return column.map(col => ({
      ...col,
      width: 100 / column.length + '%',
    }));
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '기부 상세내역 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header />}
      projectTitle={<ProjectTitle />}
      navigation={<Navigation pageId='donation' />}
      content={
        <>
          <div className='donation-page donation-infograph'>
            <div className='infograph-content'>
              <InfographicCard
                order={0}
                backgroundUrl={bgInfographic1}
                foregroundUrl={fgInfographic1}
                foregroundPosition={{
                  top: 26,
                  left: 20,
                }}
                label='누적 기부금'
                value={summaryInfo.accumulatedDonation}
                unit='원'
              />
              <InfographicCard
                order={1}
                backgroundUrl={bgInfographic2}
                foregroundUrl={fgInfographic2}
                foregroundPosition={{
                  top: 59,
                  left: 54,
                }}
                label='누적 사용금'
                value={summaryInfo.usedDonation}
                unit='원'
                containerStyle={{
                  marginLeft: '30px',
                }}
              />
              <InfographicCard
                order={2}
                backgroundUrl={bgInfographic3}
                foregroundUrl={fgInfographic3}
                foregroundPosition={{
                  top: 0,
                  left: 0,
                }}
                label='누적 대상자'
                value={summaryInfo.beneficiaryCount}
                unit='명'
                containerStyle={{
                  marginLeft: '30px',
                }}
              />
            </div>
          </div>
          <div className='donation-container mb-20'>
            <ul>
              <li>
                <h4>기부금 현황</h4>
                <div>
                  <p className='orange-title font-normal text-right orange-title'>
                    *금일 기준 누적 현황
                  </p>
                  <CommonTable
                    title='기부금 현황'
                    column={donationStatusColumn}
                    data={donationStatus}
                    summary='기부금 현황 누적 기부금, 누적 사용금, 잔여 기부금 목록'
                  />
                </div>
              </li>
              <li>
                <h4>기부내역</h4>
                <div>
                  <table className='scrolltbody'>
                    <caption className='accessible-hidden'>
                      기부내역 누적 기부금, 기부일, 기부 금액 목록
                    </caption>
                    <colgroup>
                      <col style={{ width: '35%' }} />
                      <col />
                      <col />
                    </colgroup>
                    <thead>
                      <tr>
                        <th scope='col'>누적 기부금</th>
                        <th scope='col'>기부일</th>
                        <th scope='col'>기부금액</th>
                      </tr>
                    </thead>
                    <tbody>
                      {(donationList || []).length === 0 ? (
                        <tr>
                          <td colSpan={3}>결과가 없습니다.</td>
                        </tr>
                      ) : (
                        (donationList || []).map((item, index) =>
                          index === 0 ? (
                            <tr key={shortid.generate()}>
                              <td
                                rowSpan={(donationList || []).length}
                                className='colspan-border-right text-orange font-bold'
                              >
                                {NumberUtil.formatter.commas(summaryInfo.accumulatedDonation)}원
                              </td>
                              <td>{MomnetUtil.formatter.ymd(item.donateDate)}</td>
                              <td>
                                <p className='amount-right'>
                                  {NumberUtil.formatter.commas(item.amount)}원
                                </p>
                              </td>
                            </tr>
                          ) : (
                            <tr key={shortid.generate()}>
                              <td>{MomnetUtil.formatter.ymd(item.donateDate)}</td>
                              <td>
                                <p className='amount-right'>
                                  {NumberUtil.formatter.commas(item.amount)}원
                                </p>
                              </td>
                            </tr>
                          ),
                        )
                      )}
                    </tbody>
                    <tfoot></tfoot>
                  </table>
                </div>
              </li>
              <li>
                <h4>기부금 사용내역</h4>
                <div className='text-center'>
                  <table className='scrolltbody'>
                    <caption className='accessible-hidden'>
                      기부금 사용내역 누적 사용금, 제공항목, 제공 건수, 사용 금액 목록
                    </caption>
                    <colgroup>
                      <col style={{ width: '35%' }} />
                      <col style={{ width: '25%' }} />
                      <col style={{ width: '15%' }} />
                      <col style={{ width: '25%' }} />
                    </colgroup>
                    <thead>
                      <tr>
                        <th scope='col'>누적 사용금</th>
                        <th scope='col'>제공항목</th>
                        <th scope='col'>제공건수</th>
                        <th scope='col'>사용금액</th>
                      </tr>
                    </thead>
                    <tbody>
                      {(matchingList || []).length === 0 ? (
                        <tr>
                          <td colSpan={4}>결과가 없습니다.</td>
                        </tr>
                      ) : (
                        (matchingList || []).map((item, index) => (
                          <Maybe key={shortid.generate()} test={index < hiddenfrom}>
                            {index === 0 ? (
                              <tr key={shortid.generate()}>
                                <td
                                  rowSpan={
                                    hiddenfrom < matchingList.length
                                      ? hiddenfrom
                                      : matchingList.length
                                  }
                                  className='colspan-border-right text-orange font-bold'
                                >
                                  {NumberUtil.formatter.commas(summaryInfo.usedDonation)}원
                                </td>
                                <td>{item.solutionName}</td>
                                <td>{item.beneficiaryCount}건</td>
                                <td className='text-right'>
                                  <p className='amount-right'>
                                    {NumberUtil.formatter.commas(item.amount)}원
                                  </p>
                                </td>
                              </tr>
                            ) : (
                              <tr key={shortid.generate()}>
                                <td>{item.solutionName}</td>
                                <td>{item.beneficiaryCount}건</td>
                                <td className='text-right'>
                                  <p className='amount-right'>
                                    {NumberUtil.formatter.commas(item.amount)}원
                                  </p>
                                </td>
                              </tr>
                            )}
                          </Maybe>
                        ))
                      )}
                    </tbody>
                    <tfoot></tfoot>
                  </table>
                  <Maybe test={matchingList.length > hiddenfrom}>
                    <button className='py-2 px-5' onClick={handleChangeHiddenfrom}>
                      더보기
                      <ExpandMoreRoundedIcon className='pb-1' />
                    </button>
                  </Maybe>
                  <Maybe test={hiddenfrom === matchingList.length}>
                    <button className='py-2 px-5' onClick={handleChangeHiddenfrom}>
                      닫기
                      <ExpandLessRoundedIcon className='pb-1' />
                    </button>
                  </Maybe>
                </div>
              </li>
              <li>
                <h4>대상자별 사용내역</h4>
                <div>
                  <CommonTable
                    title='대상자별 사용내역'
                    summary='대상자별 사용내역 대상자 번호, 이름, 성별, 생년, 지역, 대분류, 제공 총액, 제공 내역 목록'
                    column={getColumn() || []}
                    data={beneficiaryList}
                    usePagination
                    total={beneficiaryListTotal || 0}
                    currentPage={start}
                    onChangePage={handleChangePage}
                    pageSize={10}
                  />
                </div>
              </li>
            </ul>
          </div>
          <BeneficiaryDialog
            open={isShowBeneficiaryDialog}
            beneficiaryId={beneficiaryId}
            column={getColumn() || []}
            onClose={handleCloseBeneficiaryDialog}
          />
        </>
      }
    />
  );
};

export default DonationPage;
