import qs from 'querystring';
import RestService from '../RestService';

/**
 * 기부담당자 담당 프로젝트 목록
 * @param {number} projectId 프로젝트 아이디
 */
const getProjectList = async () => {
  const url = '/auth/project';
  const response = await RestService.get(url);
  return response;
};

/**
 * 프로젝트 개요 상세 내역
 * @param {number} projectId 프로젝트 아이디
 */
const getProjectSummary = async projectId => {
  const url = `/feedback/project/${projectId}/summary`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 기부 상세 내역
 * @param {number} projectId 프로젝트 아이디
 */
const getDonationInfoBySummary = async projectId => {
  const url = `/auth/project/${projectId}/summary`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 대상자별 상세내역
 * @param {number} projectId 프로젝트 아이디
 */
const getDonationInfoByBeneficiary = async (projectId, conditions) => {
  const url = `/auth/project/${projectId}/beneficiary?${qs.stringify(conditions)}`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 대상자별 상세정보 팝업 상세
 * @param {number} projectId 프로젝트 아이디
 */
const getDetailInfoOfBeneficiary = async projectId => {
  const url = `/auth/project/beneficiary/${projectId}`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 대상자별 상세정보 팝업 제공내역
 * @param {number} projectId 프로젝트 아이디
 */
const getDetailInfoOfSolutionByBeneficiary = async projectId => {
  const url = `/auth/project/beneficiary/${projectId}/solution`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 대상자별 목록 (필터링, 페이징, 소팅 프런트엔드 처리)
 * @param {number} projectId 프로젝트 아이디
 */
const getDetailInfoOfProjectForBeneficiary = async projectId => {
  const url = `/feedback/project/${projectId}/beneficiary`;
  const { data } = await RestService.get(url);
  return data;
};

/**
 * 대상자별 진행 과정
 * @param {number} beneficiaryId 대상자 아이디
 */
const getDetailInfoOfProgressByBeneficiary = async beneficiaryId => {
  const url = `/feedback/project/beneficiary/${beneficiaryId}/progress`;
  const { data } = await RestService.get(url);
  return data;
};

/**
 * 리포트 목록
 * @param {number} projectId
 */
const getReportListOfProject = async projectId => {
  const conditions = {
    start: 0,
    length: 9999,
  };
  const url = `/feedback/project/${projectId}/report?${qs.stringify(conditions)}`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 리포트 상세
 * @param {number} reportId 리포트 아이디
 */
const getDetailInfoOfReport = async reportId => {
  const url = `/feedback/project/report/${reportId}`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 리포트 기부 현황
 * @param {number} projectDonorId 프로젝트 기부사 아이디
 */
const getStatusOfReport = async (projectDonorId, conditions) => {
  const url = `/feedback/project/donor/${projectDonorId}/report/status?${qs.stringify(conditions)}`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 리포트 댓글 목록
 * @param {number} reportId 리포트 아이디
 */
const getCommentListOfReport = async reportId => {
  const url = `/feedback/project/report/${reportId}/comment`;
  const response = await RestService.get(url);
  return response;
};

/**
 * 리포트 댓글 등록
 * @param {number} reportId 리포트 아이디
 * @param {string} content 리포트 댓글 내용
 * @param {number} parentId 리포트 부로 댓글 일련 번호
 * @returns
 */
const createCommentOfReport = async (reportId, content, parentId = null) => {
  const url = `/feedback/project/report/${reportId}/comment`;
  const response = await RestService.post(url, {
    content,
    parent: {
      id: parentId,
    },
  });
  return response;
};

/**
 * 리포트 댓글 수정
 * @param {number} commentId 댓글 아이디
 * @param {string} content 리포트 댓글 내용
 * @param {number} parentId 리포트 부로 댓글 일련 번호
 * @returns
 */
const updateCommentOfReport = async (commentId, content, parentId = null) => {
  const url = `/feedback/project/report/comment/${commentId}`;
  const response = await RestService.put(url, {
    content,
    parent: {
      id: parentId,
    },
  });
  return response;
};

/**
 * 리포트 게시물 댓글 삭제
 * @param {number} commentId 댓글 아이디
 */
const deleteCommentOfReport = async (commentId, parentId = null) => {
  const url = `/feedback/project/report/comment/${commentId}`;
  const response = await RestService.delete(url, {
    parent: {
      id: parentId,
    },
  });
  return response;
};

