import React from 'react';
import PropTypes from 'prop-types';
import shortid from 'shortid';
import Pagination from '@material-ui/lab/Pagination';
import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';
import ExpandLessRoundedIcon from '@material-ui/icons/ExpandLessRounded';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import { Maybe } from '../../presentational';
import { GridUtil } from '../../../utils';

const styles = {
  hover: {
    cursor: 'pointer',
  },
};

const useStyles = makeStyles(theme => ({
  root: {
    '& > *': {
      marginTop: theme.spacing(2),
      borderRadius: '0px',
      color: '#666666',
    },
    '& .Mui-selected': {
      backgroundColor: 'transparent',
      color: '#e66526',
      border: '2px solid #e66526',
      fontWeight: 'bold',
    },
    '& .MuiPaginationItem-root': {
      borderRadius: '0px',
    },
  },
}));

const CommonTable = ({
  title,
  tableClassName = '',
  summary,
  emptyText = '결과가 없습니다.',
  column,
  data = [],
  totalDataColnumn,
  totalData = [],
  width = '100%',
  bodyScroll,
  onClickRow,
  usePagination = false,
  total = 0,
  pageSize = 10,
  currentPage = 0,
  onChangePage,
  useNo = false,
  hiddenfrom = 9999,
  onChangeExpanded,
}) => {
  const classes = useStyles();

  const handleClickRow = rowData => {
    if (onClickRow) {
      onClickRow(rowData);
    }
  };

  const handleChangePage = (event, page) => {
    if (onChangePage) {
      if (currentPage !== page - 1) {
        onChangePage(page - 1);
      }
    }
  };

  const handleChangeExpanded = () => {
    if (onChangeExpanded) {
      onChangeExpanded();
    }
  };

  const getItemAriaLabel = (type, page, selected) => {
    switch (type) {
      case 'first':
        return '첫 페이지로';
      case 'last':
        return '마지막 페이지로';
      case 'next':
        return '다음으로';
      case 'previous':
        return '이전으로';

      default:
        return `${page}페이지 ${selected ? '선택됨' : ''}`;
    }
  };

  return (
    <div
      style={{ width }}
      className={`flex flex-col items-center table-area ${bodyScroll ? 'body-scroll' : ''}`}
    >
      <table className={`scrolltbody ${tableClassName}`} style={{ width: '100%' }}>
        <caption className='accessible-hidden'>{summary}</caption>
        <colgroup>
          {useNo && <col style={{ width: '120px' }} key={shortid.generate()} />}
          {(column || []).map(({ width }) => {
            return width ? (
              <col style={{ width: `${width}` }} key={shortid.generate()} />
            ) : (
              <col key={shortid.generate()} />
            );
          })}
        </colgroup>
        <thead>
          <tr>
            {useNo && (
              <th id='no' scope='col'>
                NO.
              </th>
            )}
            {(column || []).map(({ id, label }) => (
              <th scope='col' key={id}>
                {label}
              </th>
            ))}
          </tr>
        </thead>
        <tfoot>
          {(totalData || []).map((rowData, rowNum) => (
            <tr key={shortid.generate()}>
              {(totalDataColnumn || []).map(
                ({ id, classNames, suffix, colspan, cellRenderer }, index) => {
                  if (cellRenderer) {
                    const CustomCell = cellRenderer(rowData, column[index], rowNum);
                    if (index === 0) {
                      return (
                        <th scope='row' key={id} className={classNames} colSpan={colspan}>
                          {CustomCell}
                        </th>
                      );
                    }
                    return (
                      <td key={id} className={classNames}>
                        {CustomCell}
                        {suffix}
                      </td>
                    );
                  }
                  if (index === 0) {
                    return (
                      <th scope='row' key={id} className={classNames} colSpan={colspan}>
                        {rowData[id]}
                      </th>
                    );
                  }
                  return (
                    <td key={id} className={classNames}>
                      {rowData[id]}
                      {suffix}
                    </td>
                  );
                },
              )}
            </tr>
          ))}
        </tfoot>
        <tbody tabIndex={bodyScroll ? 0 : -1}>
          {data.length === 0 ? (
            <tr>
              <td colSpan={useNo ? column.length + 1 : column.length}>{emptyText}</td>
            </tr>
          ) : (
            (data || []).map((rowData, rowNum) => (
              <Maybe key={shortid.generate()} test={!hiddenfrom || rowNum < hiddenfrom}>
                <tr
                  key={shortid.generate()}
                  // onClick={() => handleClickRow(rowData)}
                  style={onClickRow ? styles.hover : {}}
                >
                  {useNo && (
                    <td>{GridUtil.calcRownumDesc(total, currentPage, pageSize, rowNum)}</td>
                  )}
                  {column.map(({ id, classNames, suffix, cellRenderer, scope, link }, index) => {
                    if (cellRenderer) {
                      const CustomCell = cellRenderer(rowData, column[index], rowNum);
                      if (scope) {
                        return (
                          <th scope={scope} key={id} className={classNames}>
                            {CustomCell}
                            {suffix}
                          </th>
                        );
                      }

                      return (
                        <td key={id} className={classNames}>
                          {CustomCell}
                          {suffix}
                        </td>
                      );
                    }
                    if (scope) {
                      return (
                        <th scope={scope} key={id} className={classNames}>
                          {rowData[id]}
                          {suffix}
                        </th>
                      );
                    }
                    if (link) {
                      return (
                        <td key={id} className={classNames}>
                          <Link to={`${link}/${rowData.id}`}>
                            {rowData[id]}
                            {suffix}
                          </Link>
                        </td>
                      );
                    }

                    return (
                      <td key={id} className={classNames}>
                        {rowData[id]}
                        {suffix}
                      </td>
                    );
                  })}
                </tr>
              </Maybe>
            ))
          )}
        </tbody>
      </table>
      <Maybe test={!!usePagination}>
        <div className={`${classes.root} relative top-8`}>
          <Pagination
            showFirstButton
            showLastButton
            count={total ? Math.ceil(total / pageSize) : 1}
            page={Math.ceil(currentPage / 10) + 1}
            defaultPage={5}
            onChange={handleChangePage}
            getItemAriaLabel={(type, page, selected) => getItemAriaLabel(type, page, selected)}
          />
        </div>
      </Maybe>
      <Maybe test={hiddenfrom !== 9999}>
        <Maybe test={data.length > hiddenfrom}>
          <button className='py-2 px-5' onClick={handleChangeExpanded}>
            더보기
            <ExpandMoreRoundedIcon className='pb-1' />
          </button>
        </Maybe>
        <Maybe test={hiddenfrom === 9999}>
          <button className='py-2 px-5' onClick={handleChangeExpanded}>
            닫기
            <ExpandLessRoundedIcon className='pb-1' />
          </button>
        </Maybe>
      </Maybe>
    </div>
  );
};

CommonTable.propTypes = {
  title: PropTypes.string.isRequired,
  tableClassName: PropTypes.string,
  summary: PropTypes.string.isRequired,
  emptyText: PropTypes.string,
  column: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      label: PropTypes.string,
      scope: PropTypes.oneOf(['row', 'col']),
      suffix: PropTypes.string,
      classNames: PropTypes.string,
      width: PropTypes.number,
      cellRenderer: PropTypes.func,
      hiddenfrom: PropTypes.number,
      link: PropTypes.string,
    }),
  ).isRequired,
  data: PropTypes.array,
  totalData: PropTypes.array,
  width: PropTypes.string,
  bodyScroll: PropTypes.bool,
  usePagination: PropTypes.bool,
  onChangeExpanded: PropTypes.func,
};

export default CommonTable;
