import React from 'react';
import PropTypes from 'prop-types';

const SwitchCase = ({ tests, defaultComponent }) => {
  const filteredTarget = tests.find(({ test }) => test);
  let targetNode;

  if (filteredTarget) {
    targetNode = filteredTarget.component;
  } else if (defaultComponent) {
    targetNode = defaultComponent;
  } else {
    targetNode = null;
  }
  return <>{targetNode}</>;
};

SwitchCase.propTypes = {
  tests: PropTypes.arrayOf(
    PropTypes.shape({
      test: PropTypes.bool.isRequired,
      component: PropTypes.node.isRequired,
    }),
  ),
  defaultComponent: PropTypes.node,
};

export default SwitchCase;
