import './style.scss';
import React, { useEffect, useState } from 'react';
import HttpStatus from 'http-status';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { Header } from '../../components/container';
import { Footer } from '../../components/presentational';
import { setAuthenticated } from '../../store/slices/globalSlice';
import { AuthService } from '../../services';
import Constants from '../../constants';
import { StringUtil } from '../../utils';

const IntroPage = () => {
  const history = useHistory();
  const dispatch = useDispatch();

  const authenticated = useSelector(({ global }) => global.authenticated);

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleChangeUsername = e => setUsername(StringUtil.formatter.noSpace(e.target.value));
  const handleChangePassword = e => setPassword(StringUtil.formatter.noSpace(e.target.value));

  const validate = () => {
    let msg = '';

    if (!username || !password) {
      msg = Constants.message.Al_00_01;
    }

    if (msg) {
      alert(msg);
      return false;
    }
    return true;
  };

  const handleSubmit = async e => {
    e.preventDefault();

    if (!validate()) {
      return;
    }

    try {
      const payload = {
        username,
        password,
      };
      await AuthService.signIn(payload);
      dispatch(setAuthenticated(true));
    } catch (error) {
      if (error.error_message) {
        let msg = '';

        if (error.error_code === Constants.code.WRONG_PASSWORD) {
          msg = Constants.message.Al_00_01;
        } else if (error.error_code === Constants.code.CANNOT_FOUND_ID) {
          msg = Constants.message.Al_00_01;
        } else if (error.error_code === Constants.code.DELETED_USER) {
          msg = Constants.message.Al_00_01;
        } else {
          msg = error.error_message;
        }

        alert(msg);
      }

      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  useEffect(() => {
    if (authenticated) {
      history.replace('/summary');
    }
  }, []);

  return (
    <div>
      <div className='skip-navigation'>
        <a href='#content'>본문 바로가기</a>
      </div>
      <Header />
      <div className='intro-wrap' id='content'>
        <div className='intro-top-wrapper'>
          <div className='content-wrapper'>
            <h2>세상파일 프로젝트 리포트</h2>
            <p>
              프로젝트에 투입된 자원을 투명하게 공개하고 관리하는
              <br />
              세상파일의 차별화된 시스템입니다.
            </p>
            <div className='form-wrapper'>
              <form noValidate onSubmit={handleSubmit}>
                <fieldset>
                  <legend className='accessible-hidden'>로그인</legend>
                  <label className='accessible-hidden' htmlFor='username'>
                    아이디
                  </label>
                  <input
                    required
                    type='text'
                    id='username'
                    name='username'
                    placeholder='아이디'
                    maxLength={25}
                    value={username}
                    onChange={handleChangeUsername}
                    autoComplete='on'
                  />
                  <label className='accessible-hidden' htmlFor='password'>
                    비밀번호
                  </label>
                  <input
                    required
                    type='password'
                    id='password'
                    name='password'
                    placeholder='비밀번호'
                    maxLength={25}
                    value={password}
                    onChange={handleChangePassword}
                    autoComplete='on'
                  />
                </fieldset>
                <button type='submit'>
                  <span>로그인</span>
                </button>
              </form>
              <p className='form-desc'>
                세상파일 프로젝트 리포트는 관리자를 통해 가입하고 이용하실 수 있습니다.
              </p>
              <p className='form-contact'>
                <strong>이용문의</strong>

                <a href='mailto:sesang-file@skhappiness.org' target='_blank' rel='noreferrer'>
                  sesang-file@skhappiness.org
                </a>
              </p>
            </div>
          </div>
        </div>
        <div className='intro-item-wrapper'>
          <section>
            <i className='ico01' />
            <h3>프로젝트 개요</h3>
            <p>
              프로젝트 개요와 현황, 경과 및<br />
              결과를 확인하실 수 있습니다.
            </p>
          </section>
          <section>
            <i className='ico02' />
            <h3>기부 상세내역</h3>
            <p>
              기부내역과 사용내역을 대상자별로
              <br />
              상세하게 확인하실 수 있습니다.
            </p>
          </section>
          <section>
            <i className='ico03' />
            <h3>프로젝트 리포트</h3>
            <p>
              기부금 사용현황 리포트를
              <br />
              정기적으로 받아보실 수 있습니다.
            </p>
          </section>
          <section>
            <i className='ico04' />
            <h3>커뮤니케이션</h3>
            <p>
              후원 파트너와 세상파일의 원활한
              <br />
              소통을 위한 커뮤니케이션 채널입니다.{' '}
            </p>
          </section>
        </div>

        <Footer />
      </div>
    </div>
  );
};

export default IntroPage;
