import './style.scss';
import React, { useState, useEffect } from 'react';
import HttpStatus from 'http-status';
import { useHistory, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { setAuthenticated } from '../../store/slices/globalSlice';
import { Contents, Navigation, Header, ReactSunEditor } from '../../components/container';
import { Layout, Maybe } from '../../components/presentational';
import { ProjectService, CommonService } from '../../services';
import Constants from '../../constants';
import xIcon from '../../assets/images/icon/x-icon.png';
import { StringUtil } from '../../utils';

const CommunicationEditPage = () => {
  const params = useParams();
  const dispatch = useDispatch();
  const history = useHistory();
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);
  const defaultProject = useSelector(({ project }) => project.defaultProject);
  const [postId, setPostId] = useState(params.id);
  const [projectId, setProjectId] = useState(defaultProject.id);
  const [isSentEmail, setIsSentEmail] = useState(true);
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [initialFiles, setInitialFiles] = useState([]);
  const [files, setFiles] = useState([]);
  const [donorAccountList, setDonorAccountList] = useState([]);
  const [feedbackBoardAttachList, setFeedbackBoardAttachList] = useState([]);
  const [isAuthenticated, setIsAuthenticated] = useState(true);

  const getCommunicationPost = async () => {
    try {
      const response = await ProjectService.getCommunicationPost(postId);

      setProjectId(response.project.id);
      setIsAuthenticated(response.regId === simpleUserInfo.username);
      setTitle(response.title);
      setContent(response.content);
      setDonorAccountList(
        response.feedbackBoardDonorAccountList.map(item => ({ id: item.donorAccount.id })),
      );
      setFiles(
        response.feedbackBoardAttachList.map(item => ({
          id: item.id,
          name: item.originFileName,
          path: item.path,
        })),
      );
      setInitialFiles(response.feedbackBoardAttachList);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const moveToCommunicationDetailPage = e => {
    e.preventDefault();
    history.goBack();
  };
  const onChangeTitle = e => setTitle(e.target.value);

  const handleChangeEditor = html => {
    setContent(html);
  };
  const contentValidator = () => {
    let msg;

    if (StringUtil.formatter.noSpace(title) === '') {
      msg = Constants.message.Al_01_01;
    } else if (StringUtil.formatter.htmlToText(content).length === 0) {
      msg = Constants.message.Al_01_02;
    }

    if (msg) {
      alert(msg);
      return false;
    }
    return true;
  };

  const fileValidator = (fileList = []) => {
    const allowedExtensions = [
      'jpg',
      'jpeg',
      'png',
      'gif',
      'zip',
      'hwp',
      'docx',
      'pptx',
      'xlsx',
      'pdf',
    ];
    const singleSizeLimit = 10 * 1024 * 1024; // 10MB;
    const allSizeLimit = 30 * 1024 * 1024; // 30MB;
    const unitLimit = 3; // 3개;
    const hasInvalidFileSize = fileList.findIndex(file => file.size > singleSizeLimit) > -1;
    const allFileSize =
      files.reduce((acc, file) => acc + file.size, 0) +
      fileList.reduce((acc, file) => acc + file.size, 0);
    const hasDisallowExt =
      fileList.findIndex(file => {
        const ext = file.name.split('.').pop();
        return !allowedExtensions.includes(ext.toLowerCase());
      }) > -1;

    let msg;

    if (files.length + fileList.length > unitLimit) {
      msg = Constants.message.Al_01_11;
    } else if (hasDisallowExt) {
      msg = Constants.message.Al_01_04;
    } else if (hasInvalidFileSize || allFileSize > allSizeLimit) {
      msg = Constants.message.Al_01_03;
    }

    if (msg) {
      alert(msg);
      return false;
    }
    return true;
  };

  const handleAddFile = e => {
    e.preventDefault();

    if (e.target.files.length > 0 && !fileValidator(Array.from(e.target.files))) {
      return;
    }
    const newFiles = files.concat(Array.from(e.target.files));
    setFiles(newFiles);
  };

  const handleRemoveFile = fileIndex => {
    const newFiles = files.filter((file, index) => index !== fileIndex);
    setFiles(newFiles);
  };

  const handleSubmit = async e => {
    e.preventDefault();
    if (!contentValidator()) {
      return;
    }

    try {
      const payload = { title, content, isSentEmail, donorAccountList, feedbackBoardAttachList };
      const directory = 'communication';

      if (files.length > 0) {
        const newUploadList = files.filter(file => !file.id);

        if (newUploadList.length > 0) {
          const response = await CommonService.uploadFile(newUploadList, directory);

          const newUploadResult = response.map(attachItem => ({
            path: `${directory}/${attachItem.folder}${attachItem.saveName}`,
            originFileName: attachItem.fileName,
          }));

          const existedUploadResult = files
            .filter(file => file.id)
            .map(file => ({ path: file.path, originFileName: file.name }));

          payload.feedbackBoardAttachList = [...existedUploadResult, ...newUploadResult];
        } else {
          payload.feedbackBoardAttachList = files.map(file => ({
            path: file.path,
            originFileName: file.name,
          }));
        }
      } else {
        payload.feedbackBoardAttachList = [];
      }

      const idxs = files.map(file => file.id);

      const filesToRemove = initialFiles
        .filter(file => !idxs.includes(file.id))
        .map(file => ({
          directory: file.path.substring(0, file.path.lastIndexOf('/')),
          fileName: file.path.split('/').pop(),
        }));

      if (filesToRemove.length > 0) {
        filesToRemove.map(async item => {
          await CommonService.deleteFile(item);
        });
      }

      const response = await ProjectService.updateCommunicationPost(postId, payload);

      if (response) {
        alert(Constants.message.Al_01_14);
        history.push(`/communication/post/${postId}`);
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      getCommunicationPost();
    } else {
      history.replace('/terms/conditions');
    }
  }, []);

  useEffect(() => {
    if (!isAuthenticated) {
      alert(Constants.message.Al_01_15);
      history.push(`/communication/post/${postId}`);
    }
  }, [isAuthenticated]);

  useEffect(() => {
    if (defaultProject.id !== projectId) {
      history.push(`/communication/list`);
    }
  }, [defaultProject]);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '커뮤니케이션 수정 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header />}
      navigation={<Navigation style={{ marginTop: '80px', top: 0 }} />}
      content={
        <Contents title='게시물 수정'>
          <div className='edit-container'>
            <form noValidate onSubmit={handleSubmit}>
              <ul>
                <li>
                  <label htmlFor='title' className='label'>
                    제목
                  </label>
                  <input
                    id='title'
                    type='text'
                    className='basic-input-text'
                    placeholder='제목을 입력해주세요.'
                    maxLength='50'
                    value={title}
                    onChange={onChangeTitle}
                  />
                </li>
                <li>
                  <span className='label'>내용</span>
                  <div>
                    <ReactSunEditor value={content} onChange={handleChangeEditor} />
                  </div>
                </li>
                <li>
                  <span className='label'>파일첨부</span>
                  <div className='input-file'>
                    <label htmlFor='attachFile' className='upload-button'>
                      파일 업로드
                    </label>
                    <input
                      multiple
                      id='attachFile'
                      type='file'
                      accept='.zip, .hwp, .docx, .pptx, .xlsx, .pdf, image/jpg, image/jpeg, image/png, image/gif'
                      onChange={handleAddFile}
                      className='accessible-hidden'
                    />
                  </div>
                  <div></div>
                  <Maybe test={files.length > 0}>
                    <ul className='fileList flex flex-col pl-3 py-2'>
                      {files.map((file, index) => (
                        <li key={index.toString()} className='inline-flex items-baseline'>
                          <p className='fileList-name'>{file.name}</p>
                          <button
                            type='button'
                            className='fileList-remove-button'
                            onClick={() => handleRemoveFile(index)}
                          >
                            <img
                              src={xIcon}
                              alt='첨부파일 삭제 아이콘 이미지'
                              width='10'
                              height='10'
                            />
                          </button>
                        </li>
                      ))}
                    </ul>
                  </Maybe>
                </li>
              </ul>
              <div className='flex justify-center mb-36'>
                <a
                  href={`/communication/post/${postId}`}
                  title='취소'
                  className='orange-button-outlined mx-1'
                  onClick={moveToCommunicationDetailPage}
                >
                  취소
                </a>
                <input
                  type='submit'
                  value='등록'
                  className='orange-button-filled mx-1'
                  title='등록'
                />
              </div>
            </form>
          </div>
        </Contents>
      }
    />
  );
};

export default CommunicationEditPage;
