import './style.scss';
import React, { useState, useEffect } from 'react';
import HttpStatus from 'http-status';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import Constants from '../../constants';
import logo from '../../assets/images/logo/logo.svg';
import { AuthService } from '../../services';
import { setAuthenticated, setSimpleUserInfo, setAlarmList } from '../../store/slices/globalSlice';
import {
  TermsOfPrivacyConfirmContents,
  TermsOfServiceContents,
} from '../../components/presentational';

const { DEFAULT_PROJECT_KEY } = Constants.storage;

const TermsOfConditionsPage = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);

  const [isConfirmFull, setIsConfirmFull] = useState(false);
  const [isConfirmServiceTerm, setIsConfirmServiceTerm] = useState(false);
  const [isConfirmPrivacyTerm, setIsConfirmPrivacyTerm] = useState(false);

  const onChangeIsConfirmFull = e => {
    setIsConfirmFull(e.target.checked);
    if (e.target.checked) {
      setIsConfirmServiceTerm(true);
      setIsConfirmPrivacyTerm(true);
    } else {
      setIsConfirmServiceTerm(false);
      setIsConfirmPrivacyTerm(false);
    }
  };

  const onChangeIsServiceTerm = e => {
    setIsConfirmServiceTerm(e.target.checked);
    if (!e.target.checked) {
      setIsConfirmFull(false);
    } else {
      isConfirmPrivacyTerm ? setIsConfirmFull(true) : setIsConfirmFull(false);
    }
  };

  const onChangeIsPrivacyTerm = e => {
    setIsConfirmPrivacyTerm(e.target.checked);
    if (!e.target.checked) {
      setIsConfirmFull(false);
    } else {
      isConfirmServiceTerm ? setIsConfirmFull(true) : setIsConfirmFull(false);
    }
  };

  const validate = () => {
    let msg = '';
    if (!isConfirmServiceTerm) {
      msg = Constants.message.Al_04_03;
    } else if (!isConfirmPrivacyTerm) {
      msg = Constants.message.Al_04_04;
    }

    if (msg) {
      alert(msg);
      return false;
    }
    return true;
  };

  const handleSubmit = async e => {
    e.preventDefault();

    if (!validate()) {
      return;
    }

    if (window.confirm(Constants.message.Al_04_01)) {
      try {
        const response = await AuthService.updateAccountTerms(simpleUserInfo.id);
        if (response.success_message === 'process success.') {
          dispatch(
            setSimpleUserInfo({
              id: simpleUserInfo.id,
              email: simpleUserInfo.email,
              name: simpleUserInfo.name,
              rank: simpleUserInfo.rank,
              username: simpleUserInfo.username,
              donor: simpleUserInfo.donor,
              isAgreeTerms: true,
            }),
          );
          alert(Constants.message.Al_04_02);
          history.replace('/summary');
        }
      } catch (error) {
        if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
          dispatch(setAuthenticated(false));
        }
      }
    }
  };

  const handleSignOut = async e => {
    e.preventDefault();
    await AuthService.signOut();
    dispatch(setAuthenticated(false));
    dispatch(
      setSimpleUserInfo({
        id: '',
        email: '',
        name: '',
        rank: '',
        username: '',
        donor: {},
        isAgreeTerms: false,
      }),
    );
    dispatch(setAlarmList([]));
    sessionStorage.removeItem(DEFAULT_PROJECT_KEY);
    history.replace('/intro');
  };

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      history.replace('/intro');
    }
  }, [simpleUserInfo]);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '약관 동의 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <>
      <div className='skip-navigation'>
        <a href='#content'>본문 바로가기</a>
      </div>
      <div className='terms-container' id='content'>
        <div className='background'>
          <div className='mark-container'></div>
          <div className='diagonal' />
        </div>

        <div className='contents-container'>
          <article className='contents-body'>
            <h1 className='accessible-hidden'>세상파일 프로젝트 리포트</h1>
            <img className='logo' src={logo} alt='세상파일 프로젝트' />
            <h2>이용 약관</h2>

            <form noValidate onSubmit={handleSubmit}>
              <div className='terms-form'>
                {/* 전체 동의 */}
                <section>
                  <div className='checkbox bold'>
                    <input
                      type='checkbox'
                      id='isConfirmFull'
                      name='isConfirmFull'
                      checked={isConfirmFull}
                      onChange={onChangeIsConfirmFull}
                    />
                    <label htmlFor='isConfirmFull'>이용약관 전체동의</label>
                  </div>
                </section>

                {/* 이용약관 동의 */}
                <section>
                  <div className='checkbox'>
                    <input
                      type='checkbox'
                      id='isConfirmServiceTerm'
                      name='isConfirmServiceTerm'
                      checked={isConfirmServiceTerm}
                      onChange={onChangeIsServiceTerm}
                    />
                    <label htmlFor='isConfirmServiceTerm'>이용약관에 동의합니다.</label>
                  </div>

                  <div className='contents' tabIndex='0'>
                    <TermsOfServiceContents size='small' />
                  </div>
                </section>

                {/* 개인정보처리방침 동의 */}
                <section>
                  <div className='checkbox'>
                    <input
                      type='checkbox'
                      id='isConfirmPrivacyTerm'
                      name='isConfirmPrivacyTerm'
                      checked={isConfirmPrivacyTerm}
                      onChange={onChangeIsPrivacyTerm}
                    />
                    <label htmlFor='isConfirmPrivacyTerm'>
                      개인정보 수집 및 이용에 동의합니다.
                    </label>
                  </div>

                  <div className='contents' tabIndex='0'>
                    <TermsOfPrivacyConfirmContents size='small' />
                  </div>
                </section>
              </div>

              <div className='button-container'>
                <button type='submit' className='agree'>
                  <span>동의</span>
                </button>
                <button type='button' className='disagree' onClick={handleSignOut}>
                  <span>동의하지 않음</span>
                </button>
              </div>
            </form>
          </article>
        </div>
      </div>
    </>
  );
};

export default TermsOfConditionsPage;
