import './style.scss';
import React, { useState, useRef } from 'react';
import renderHTML from 'react-render-html';
import { MomnetUtil, StringUtil } from '../../../utils';
import { Maybe, SwitchCase } from '../../presentational';
import Constants from '../../../constants';

import useDidMountEffect from '../../presentational/useDidMountEffect';

const RecommentItem = ({ commentId, recomment, onEditComment, onDeleteCommnet }) => {
  const [isModify, setIsModify] = useState(false);
  const [newContent, setnewContent] = useState(recomment.content);

  const buttonRef = useRef(null);
  const textareaRef = useRef(null);

  const handleChangeContent = e => setnewContent(e.target.value);

  const handleFocus = () => {
    if (isModify) {
      setTimeout(() => {
        textareaRef?.current?.focus();
      }, 100);
    } else {
      setTimeout(() => {
        buttonRef?.current?.focus();
      }, 100);
    }
  };

  const handleModify = () => {
    setIsModify(!isModify);
  };

  const handleEditCancel = e => {
    e.preventDefault();
    handleModify();
    setnewContent(recomment.content);
  };

  const handleEditComment = async e => {
    e.preventDefault();

    if (StringUtil.formatter.noSpace(newContent) === '') {
      alert(Constants.message.Al_01_13);
      return;
    }

    if (onEditComment) {
      if (onEditComment(recomment.id, newContent, commentId)) {
        handleModify();
      }
    }
  };

  const handleDeleteComment = async e => {
    e.preventDefault();

    if (onDeleteCommnet) {
      const isConfirm = window.confirm(Constants.message.Al_01_09);
      if (isConfirm) {
        onDeleteCommnet(recomment.id, commentId);
      }
    }
  };

  useDidMountEffect(() => {
    handleFocus();
  }, [isModify]);

  return (
    <li className='recomment-wrap flex py-5'>
      <div className='ml-5 w-full'>
        <div className='flex pb-1'>
          <b className='comment-name'>
            {recomment.regCompany} {recomment.regName} {recomment.regRank}
          </b>
          <span className='comment-date'>
            <span>{MomnetUtil.formatter.date(recomment.createDt, 'YYYY-MM-DD')}</span>
            <span className='comment-time'>
              {MomnetUtil.formatter.date(recomment.createDt, 'HH:mm:ss')}
            </span>
          </span>
          <Maybe test={recomment.creatorFlag && !isModify}>
            <div className='ml-auto'>
              <button className='gray-edit-outlined' onClick={handleModify} ref={buttonRef}>
                <span>수정</span>
              </button>
              <button className='gray-delete-outlined' onClick={handleDeleteComment}>
                <span>삭제</span>
              </button>
            </div>
          </Maybe>
        </div>
        <SwitchCase
          tests={[
            {
              test: isModify,
              component: (
                <form noValidate onSubmit={e => handleEditComment(e)}>
                  <fieldset>
                    <legend className='accessible-hidden'>댓글 수정하기</legend>
                    <label className='accessible-hidden' htmlFor={`recomment-${recomment.id}`}>
                      댓글 수정하기
                    </label>
                    <textarea
                      id={`recomment-${recomment.id}`}
                      placeholder='댓글을 입력해주세요.'
                      onChange={handleChangeContent}
                      maxLength='500'
                      ref={textareaRef}
                    >
                      {newContent}
                    </textarea>
                    <div className='py-5 text-right'>
                      <button className='orange-button-outlined' onClick={handleEditCancel}>
                        <span>취소</span>
                      </button>
                      <input type='submit' className='orange-button-filled ml-3' value='등록' />
                    </div>
                  </fieldset>
                </form>
              ),
            },
          ]}
          defaultComponent={
            <div>{renderHTML(StringUtil.formatter.lineBreak(recomment.content))}</div>
          }
        />
      </div>
    </li>
  );
};

export default RecommentItem;
