import './style.scss';
import React, { useState } from 'react';
import shortid from 'shortid';
import { useDispatch, useSelector } from 'react-redux';
import { setDefaultProject } from '../../../store/slices/projectSlice';
import Constants from '../../../constants';

const { DEFAULT_PROJECT_KEY } = Constants.storage;

const ProjectSelect = () => {
  const dispatch = useDispatch();
  const projectList = useSelector(({ project }) => project.projectList);
  const defaultProject = useSelector(({ project }) => project.defaultProject);

  let timeOutId = null;

  const [isOpened, setIsOpened] = useState(false);

  const handleChangeProject = index => {
    const project = (projectList || []).find(proj => proj.id === Number(index));
    dispatch(setDefaultProject(project));
    sessionStorage.setItem(DEFAULT_PROJECT_KEY, project.id);
  };

  const handleToggleSelect = () => {
    setIsOpened(prevCheck => !prevCheck);
  };

  const onBlurHandler = () => {
    timeOutId = setTimeout(() => {
      setIsOpened(false);
    });
  };

  const onFocusHandler = () => {
    clearTimeout(timeOutId);
  };

  return (
    <div className='project-select' onBlur={() => onBlurHandler()} onFocus={() => onFocusHandler()}>
      <button
        type='button'
        className={`select-title ${isOpened ? 'open' : ''}`}
        onClick={() => handleToggleSelect()}
        role='combobox'
        aria-labelledby='select_lb'
        aria-haspopup='listbox'
        aria-controls='li_sort'
        title='프로젝트 선택'
        aria-expanded={!!isOpened}
      >
        <span>{defaultProject.name}</span>
      </button>
      <ul
        title='프로젝트 리스트'
        id='li_sort'
        role='listbox'
        tabIndex='-1'
        aria-labelledby='select_lb'
        aria-activedescendant='li_sort01'
      >
        {(projectList || []).map(proj => (
          <li key={shortid.generate()} role='none'>
            <button
              type='button'
              role='option'
              aria-selected={proj.id === defaultProject.id}
              onClick={() => {
                handleChangeProject(proj.id);
                handleToggleSelect();
              }}
            >
              <span>
                {proj.name}
                {proj.detailName ? ` - ${proj.detailName}` : ''}
              </span>
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default ProjectSelect;
