import './style.scss';
import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

const AlarmListItem = ({ read, message, createDt, onClick }) => {
  const handleClick = e => {
    e.preventDefault();
    onClick();
  };

  return (
    <li className='alarm-list-item'>
      <a href='#none' title={message} onClick={handleClick}>
        <p>
          {!read && (
            <i className='marker'>
              <span className='accessible-hidden'>읽지 않은 알림</span>
            </i>
          )}
          {message}
        </p>
        <span>{moment(createDt).format('YYYY-MM-DD')}</span>
        <span>{moment(createDt).format('HH:mm:ss')}</span>
      </a>
    </li>
  );
};

AlarmListItem.propTypes = {
  read: PropTypes.bool,
  message: PropTypes.string.isRequired,
  createDt: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
};

export default AlarmListItem;
