import moment from 'moment';
import { htmlToText } from 'html-to-text';
import {
  REGEX_EMAIL,
  REGEX_MOBILE,
  REGEX_LINE_BREAK,
  REGEX_SPACE,
  REGEX_NUMBER,
} from '../constants/regex';

/**
 * StringUtil
 * @author deokchanjung
 */
const formatter = {
  date: (value, format) => moment(value).format(format),
  atob: value => atob(value),
  btoa: value => btoa(unescape(encodeURIComponent(value))),
  noSpace: value => value.toString().replace(REGEX_SPACE, ''),
  onlyNumber: value => value.toString().replace(REGEX_NUMBER, ''),
  htmlToText: value => htmlToText(value).replace(REGEX_SPACE, ''),
  lineBreak: value => value.replace(REGEX_LINE_BREAK, '<br />'),
};

const validator = {
  email: value => REGEX_EMAIL.test(value),
  mobile: value => REGEX_MOBILE.test(value),
};

export default { formatter, validator };
