import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';
import { AuthService, NotificationService, ProjectService } from '../../../services';
import {
  setAuthenticated,
  setSimpleUserInfo,
  setAlarmList,
} from '../../../store/slices/globalSlice';
import { setProjectList, setDefaultProject } from '../../../store/slices/projectSlice';
import TokenService from '../../../services/TokenService';
import Constants from '../../../constants';

const ROOT_PAGE = '/intro';

const { DEFAULT_PROJECT_KEY } = Constants.storage;

const AppInitializer = ({ history, location, children }) => {
  const dispatch = useDispatch();
  const authenticated = useSelector(({ global }) => global.authenticated);

  const [loaded, setLoaded] = useState(false);

  const getAccountInfo = async () => {
    const response = await AuthService.getAccountInfo();

    dispatch(
      setSimpleUserInfo({
        id: response.data.id,
        email: response.data.email,
        name: response.data.name,
        rank: response.data.rank,
        username: response.data.username,
        donor: response.data.donor,
        // isAgreeTerms: response.data.isAgreeTerms,
        isAgreeTerms: true,
      }),
    );
  };

  const getProjectList = async () => {
    const response = await ProjectService.getProjectList();

    if (response.data) {
      if (response.data.list.length === 0) {
        alert(Constants.message.Al_01_16);
        throw new Error(Constants.message.Al_01_16);
      }

      const prevProject = (response.data.list || []).find(
        proj => proj.id === Number(sessionStorage.getItem(DEFAULT_PROJECT_KEY)),
      );

      dispatch(setProjectList(response.data.list || []));

      if (!prevProject) {
        dispatch(setDefaultProject(response.data.list[0]));
        sessionStorage.setItem(DEFAULT_PROJECT_KEY, response.data.list[0].id);
      } else {
        dispatch(setDefaultProject(prevProject));
      }
    }
  };

  const getAlarmList = async () => {
    const conditions = {
      start: 0,
      length: 5,
      isChecked: false,
    };
    const response = await NotificationService.getNotificationList(conditions);

    if (response.data) {
      dispatch(setAlarmList(response.data.list || []));
    }
  };

  useEffect(async () => {
    try {
      await getAccountInfo();
      dispatch(setAuthenticated(true));
    } catch (error) {
      TokenService.removeAccessToken();
      dispatch(setAuthenticated(false));

      history.push(ROOT_PAGE, {
        referrer: location.pathname,
      });
      setLoaded(true);
    }
  }, []);

  useEffect(async () => {
    if (authenticated) {
      try {
        await Promise.all([getAccountInfo(), getProjectList(), getAlarmList()]);
        if (location.state) {
          if (location.state.referrer !== ROOT_PAGE || location.state.referrer !== '/intro') {
            history.push(location.state.referrer || '/summary');
          } else {
            history.push('/summary');
          }
        } else if (location.pathname !== ROOT_PAGE) {
          history.push(location.pathname || '/summary');
        } else {
          history.push('/summary');
        }
      } catch (error) {
        dispatch(setAuthenticated(true));
      } finally {
        setLoaded(true);
      }
    }

    if (!authenticated && loaded) {
      TokenService.removeAccessToken();
      history.push(ROOT_PAGE, {
        referrer: location.pathname,
      });
    }
  }, [authenticated]);

  return loaded ? children : null;
};

export default withRouter(AppInitializer);
