import React from 'react';
import PropTypes from 'prop-types';
import { SwitchCase } from '../../presentational';
import ProgressBarStep from '../progressbar-step';

const BeneficiaryProgressBar = ({ list }) => {
  return (
    <SwitchCase
      tests={[
        {
          test: list.length === 1,
          component: <ProgressBarStep total={1} step={1} list={list[0] || {}} />,
        },
        {
          test: list.length === 2,
          component: (
            <>
              <ProgressBarStep total={2} step={1} list={list[0] || {}} />
              <ProgressBarStep total={2} step={2} list={list[1] || {}} />
            </>
          ),
        },
        {
          test: list.length === 3,
          component: (
            <>
              <ProgressBarStep total={3} step={1} list={list[0] || {}} />
              <ProgressBarStep total={3} step={2} list={list[1] || {}} />
              <ProgressBarStep total={3} step={3} list={list[2] || {}} />
            </>
          ),
        },
      ]}
      defaultComponent={<></>}
    />
  );
};

BeneficiaryProgressBar.propTypes = {
  list: PropTypes.array.isRequired,
};

export default BeneficiaryProgressBar;
