import './style.scss';
import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import shortid from 'shortid';
import renderHTML from 'react-render-html';
import { NumberUtil } from '../../../utils';

const { commas } = NumberUtil.formatter;

const Report = ({ id, project = {}, status = {}, data = {} }) => {
  const getPeriod = (stDate, edDate) => {
    const fmtStDate = moment(stDate).format('YYYY-MM-DD');
    const fmtEdDate = moment(edDate).format('YYYY-MM-DD');
    return `${fmtStDate} ~ ${fmtEdDate}`;
  };

  const calcPercent = (num = 0) => parseFloat(num.toFixed(1));

  return data ? (
    <article className='report-wrap' id={id}>
      {/* Report Header */}
      <section className='report-wrap--header'>
        <h4 className='report-wrap--header--donor-name orange'>
          {`${project?.name} ${project?.detailName || ''}`}
        </h4>
        <div className='report-wrap--header--meta-wrap'>
          <h2 className='report-wrap--header--title'>{data.title}</h2>

          <p>
            <span>집계기준:&nbsp;{getPeriod(data.standardStartDate, data.standardEndDate)}</span>
            <span>
              작성자:&nbsp;{data.modInfo ? data.modInfo?.description : data.regInfo?.description}
            </span>
          </p>
        </div>
      </section>

      {/* Report Body */}
      <section className='report-wrap--body'>
        <ul className='report-wrap--body--list'>
          {/* 기부금 현황 */}
          <li className='report-wrap--body--list--row'>
            <h3>기부금 현황</h3>
            <div className='aggr-wrap'>
              <div className='aggr-wrap--summary'>
                <table>
                  <caption>기부금 현황, 누적 기부금, 누적 사용금, 잔여 기부금 내역</caption>
                  <tbody>
                    <tr>
                      <th scope='row'>누적 기부금</th>
                      <td>{commas(data.accumulatedDonation || 0)} 원</td>
                    </tr>
                    <tr>
                      <th scope='row'>누적 사용금</th>
                      <td>{commas(data.usedDonation || 0)} 원</td>
                    </tr>
                    <tr>
                      <th scope='row'>잔여 기부금</th>
                      <td>{commas(data.remainingDonation || 0)} 원</td>
                    </tr>
                  </tbody>
                </table>
              </div>

              <div className='aggr-wrap--progress-wrap'>
                <div className='flex items-center justify-between'>
                  <h5>누적 사용률</h5>
                  <p>
                    <span className='aggr-wrap--progress-wrap--ratio'>
                      {calcPercent(data.accumulatedUtilization || 0)}
                    </span>
                    <span className='aggr-wrap--progress-wrap--unit'>%</span>
                  </p>
                </div>
                <div className='aggr-wrap--progress-wrap--progressbar'>
                  <i
                    className='progress'
                    style={{ width: `${calcPercent(data.accumulatedUtilization || 0)}%` }}
                  >
                    <span className='accessible-hidden'>
                      누적 사용률 {calcPercent(data.accumulatedUtilization || 0)}%
                    </span>
                  </i>
                </div>
                <div className='flex items-center justify-between'>
                  <h5>집계기준 사용금</h5>
                  <p>
                    <span className='text-orange'>{commas(data.usedDonationByDate || 0)}</span>
                    <span className='text-orange'> 원</span>
                  </p>
                </div>
              </div>
            </div>
          </li>

          {/* 기부금 사용내역 */}
          <li className='report-wrap--body--list--row'>
            <h3>기부금 사용내역</h3>
            <div>
              <table className='tbl-breakdown'>
                <caption>
                  기부금 사용내역 no, 제공항목, 금액(원), 제공건수, 사용금액(원) 목록
                </caption>
                <thead>
                  <tr>
                    <th scope='col'>NO.</th>
                    <th scope='col'>제공항목</th>
                    <th scope='col'>금액(원)</th>
                    <th scope='col'>제공건수</th>
                    <th scope='col'>사용금액(원)</th>
                  </tr>
                </thead>
                <tbody>
                  {(data.reportDataList || []).map(
                    ({ name, amount, quantity, usedAmount }, index) => (
                      <tr key={shortid.generate()}>
                        <td>{index + 1}</td>
                        <td>{name}</td>
                        <td>{commas(amount)}</td>
                        <td>{commas(quantity)}</td>
                        <td>{commas(usedAmount)}</td>
                      </tr>
                    ),
                  )}
                </tbody>
              </table>
            </div>
          </li>

          {/* 추가정보 */}
          <li className='report-wrap--body--list--row'>
            <h3>{data.additionalTitle}</h3>
            <div className='additional-info-wrap'>{renderHTML(data.additionalContent || '')}</div>
          </li>
        </ul>
      </section>
    </article>
  ) : null;
};

Report.propTypes = {
  id: PropTypes.string.isRequired,
  project: PropTypes.shape({
    name: PropTypes.string,
    detailName: PropTypes.string,
  }),
  status: PropTypes.shape({
    accumulatedDonation: PropTypes.number,
    usedDonation: PropTypes.number,
    remainingDonation: PropTypes.number,
    accumulatedUtilization: PropTypes.number,
    beneficiaryList: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string.isRequired,
        amount: PropTypes.number.isRequired,
        quantity: PropTypes.number.isRequired,
        usedAmount: PropTypes.number.isRequired,
      }),
    ),
  }),
  data: PropTypes.shape({
    id: PropTypes.number,
    title: PropTypes.string,
    standardStartDate: PropTypes.string,
    standardEndDate: PropTypes.string,
    publishDate: PropTypes.string,
    isPublished: PropTypes.bool,
    additionalTitle: PropTypes.string,
    additionalContent: PropTypes.string,
    createDt: PropTypes.string,
    regInfo: PropTypes.shape({
      id: PropTypes.number.isRequired,
      username: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
    }),
    modifyDt: PropTypes.string,
    modInfo: PropTypes.shape({
      id: PropTypes.number.isRequired,
      username: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
    }),
    projectDonor: PropTypes.shape({
      id: PropTypes.number.isRequired,
      donor: PropTypes.shape({
        id: PropTypes.number.isRequired,
        name: PropTypes.string.isRequired,
      }),
    }),
    reportAttachList: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        path: PropTypes.string.isRequired,
        originFileName: PropTypes.string.isRequired,
      }),
    ),
  }),
};

export default Report;
