import './style.scss';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import defaultDonorLogo from '../../../assets/images/logo/default_donor_logo.png';
import environments from '../../../environments';

const ProjectTitle = () => {
  const defaultProject = useSelector(({ project }) => project.defaultProject);
  const donor = useSelector(({ global }) => global.simpleUserInfo.donor);

  const [logoSrc, setLogoSrc] = useState('');

  const handleOnError = e => {
    setLogoSrc(defaultDonorLogo);
  };

  useEffect(() => {
    if (logoSrc !== donor.image) {
      setLogoSrc(donor.image ? `${environments.API_URL}upload/${donor.image}` : '');
    }
  }, [donor]);

  return (
    <div className='project-title flex justify-center'>
      <div className='flex items-center title-container'>
        <div
          className='flex justify-center items-center logo-area'
          style={{
            backgroundImage: `url(${logoSrc})`,
          }}
        >
          <img
            src={logoSrc || defaultDonorLogo}
            alt={`${donor.name} 로고`}
            onError={handleOnError}
          />
        </div>
        <h3>
          {defaultProject.name}&nbsp;
          {defaultProject.detailName}
        </h3>
      </div>
    </div>
  );
};

export default ProjectTitle;
