import moment, { months } from 'moment';

/**
 * StringUtil
 * @author deokchanjung
 */
const formatter = {
  date: (value, format) => moment(value).format(format),
  ymd: value =>
    `${moment(value).year()}년 ${moment(value).month() + 1}월 ${moment(value).date()}일`,
  day: value => {
    switch (moment(value).day()) {
      case 0: {
        return '일';
      }
      case 1: {
        return '월';
      }
      case 2: {
        return '화';
      }
      case 3: {
        return '수';
      }
      case 4: {
        return '목';
      }
      case 5: {
        return '금';
      }
      case 6: {
        return '일';
      }
      default: {
        return '';
      }
    }
  },
};

export default { formatter };
