import { REGEX_NUMBER_COMMA, REGEX_SPACE } from '../constants/regex';

/**
 * NumberUtil
 * @author deokchanjung
 */
const formatter = {
  commas: (value = 0) => {
    const [integer, decimal] = value.toString().split('.');
    const formattedNumber = integer.replace(REGEX_NUMBER_COMMA, ',');
    return decimal ? `${formattedNumber}.${decimal}` : formattedNumber;
  },
  decimal: (value = 0, index = 0) => {
    const formattedFixed = index ? Number(value).toFixed(index) : value;
    const [integer, decimal] = formattedFixed.toString().split('.');
    return decimal ? `${integer}.${decimal}` : integer;
  },
  commasAndDecimal: (value = 0, index = 0) => {
    const formattedFixed = index ? Number(value).toFixed(index) : value;
    const [integer, decimal] = formattedFixed.toString().split('.');
    const formattedNumber = integer.replace(REGEX_NUMBER_COMMA, ',');
    return decimal ? `${formattedNumber}.${decimal}` : formattedNumber;
  },
  noSpace: (value = 0) => value.toString().replace(REGEX_SPACE, ''),
};

const validator = {
  isLowerMinLength: (value, length) => value.toString().length < length,
  isUpperMaxLength: (value, length) => value.toString().length > length,
};

export default { formatter, validator };
