import './style.scss';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import HttpStatus from 'http-status';
import { Contents, Header, Navigation } from '../../components/container';
import { Layout } from '../../components/presentational';
import { AuthService } from '../../services';
import { setAuthenticated } from '../../store/slices/globalSlice';
import { StringUtil } from '../../utils';

const VerifyPwPage = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);
  const [password, setPassword] = useState('');

  const handleChangePassword = e => {
    setPassword(StringUtil.formatter.noSpace(e.target.value));
  };

  const handleSubmit = async e => {
    e.preventDefault();

    try {
      await AuthService.checkPassword(password);
      history.push('/mypage');
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
      if (error.error_message) {
        alert(error.error_message);
      }
    }
  };

  useEffect(() => {
    if (!simpleUserInfo.isAgreeTerms) {
      history.replace('/terms/conditions');
    }
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '비밀번호 확인 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header pageId='mypage' />}
      navigation={<Navigation style={{ marginTop: '80px', top: 0 }} />}
      content={
        <Contents title='마이 페이지'>
          <div className='verify-container'>
            <div className='col1'>
              <h3>비밀번호 확인</h3>
            </div>
            <div className='col2'>
              <div className='info'>
                <p>기부관리자 본인 확인을 위해 비밀번호를 확인해주세요.</p>
              </div>
              <form onSubmit={handleSubmit}>
                <input
                  type='password'
                  id='password'
                  name='password'
                  placeholder='비밀번호 입력'
                  value={password}
                  onChange={handleChangePassword}
                  title='비밀번호 확인'
                />
                <button type='submit'>
                  <span>확인하기</span>
                </button>
              </form>
            </div>
          </div>
        </Contents>
      }
    />
  );
};

export default VerifyPwPage;
