import React from 'react';
import PropTypes from 'prop-types';
import html2canvas from 'html2canvas';
import JsPDF from 'jspdf';
import { PdfButton } from '../../presentational';

const DownloadPdfButton = ({ label, targetId, filename, isIE }) => {
  const handleClickDownload = () => {
    const input = document.getElementById(targetId);

    html2canvas(input, { scrollY: isIE ? -window.pageYOffset : -window.scrollY }).then(canvas => {
      const imgData = canvas.toDataURL('image/png');
      const pdf = new JsPDF('p', 'mm', 'a4');

      const width = pdf.internal.pageSize.getWidth();
      const height = pdf.internal.pageSize.getHeight();
      const widthRatio = width / canvas.width;
      const heightRatio = height / canvas.height;
      const ratio = widthRatio > heightRatio ? heightRatio : widthRatio;

      pdf.addImage(imgData, 'PNG', 10, 10, canvas.width * ratio - 20, canvas.height * ratio - 20);
      pdf.save(`${filename}.pdf`);
    });
  };

  return (
    <div className='down-pdf-btn'>
      <div id='mm' style={{ height: '1mm' }} />
      <PdfButton label={label} onClick={handleClickDownload} />
    </div>
  );
};

DownloadPdfButton.propTypes = {
  label: PropTypes.string.isRequired,
  targetId: PropTypes.string.isRequired,
  filename: PropTypes.string.isRequired,
  isIE: PropTypes.bool.isRequired,
};

export default DownloadPdfButton;
