import React, { useRef } from 'react';
import SunEditor from 'suneditor-react';

const ReactSunEditor = ({
  onChange,
  value,
  disable = false,
  enableToolbar = true,
  showToolbar = true,
  setOptions,
}) => {
  const editorRef = useRef();

  const handleOnChangeEditor = content => {
    if (onChange) {
      onChange(content);
    }
  };

  return (
    <SunEditor
      ref={editorRef}
      lang='ko'
      showToolbar={showToolbar}
      disable={disable}
      enableToolbar={enableToolbar}
      onChange={handleOnChangeEditor}
      setDefaultStyle={`${disable ? '' : 'min-height:300px;'} height:auto;`}
      defaultValue={value}
      setContents={value}
      setOptions={{
        ...setOptions,
        buttonList: [
          [
            'bold',
            'underline',
            'italic',
            'strike',
            'list',
            'align',
            'fontSize',
            'formatBlock',
            'image',
            'link',
          ],
        ],
      }}
    />
  );
};

export default ReactSunEditor;
