/**
 * Token 관련 서비스 로직
 * @author deokchanjung(deokchan@nextculture.kr)
 */
import Axios from 'axios';
import environments from '../environments';
import Constants from '../constants';

const { STRG_ACCESS_TOKEN_KEY } = Constants.storage;

class TokenService {
  axiosConfigs = {
    baseURL: environments.API_URL,
    validateStatus: status => status < 400,
  };

  constructor() {
    this.axios = Axios.create(this.axiosConfigs);
  }

  /**
   * 로컬 스토리지에서 엑세스 토큰 가져오기
   */
  getAccessToken = () => sessionStorage.getItem(STRG_ACCESS_TOKEN_KEY);

  /**
   * 로컬 스토리지로 액세스 토큰 저장
   * @param accessToken
   */
  setAccessToken = accessToken => sessionStorage.setItem(STRG_ACCESS_TOKEN_KEY, accessToken);

  /**
   * 로컬 스토리지에 저장된 액세스 토큰 삭제
   * @return {void}
   */
  removeAccessToken = () => sessionStorage.removeItem(STRG_ACCESS_TOKEN_KEY);
}

export default new TokenService();
