import './style.scss';
import React, { useState } from 'react';
import Popover from '@material-ui/core/Popover';
import { withStyles } from '@material-ui/core/styles';
import { useDispatch, useSelector } from 'react-redux';
import shortid from 'shortid';
import moment from 'moment';
import { Link, useHistory } from 'react-router-dom';
import { NotificationService } from '../../../services';
import { setAlarmList } from '../../../store/slices/globalSlice';
import { Maybe } from '../../presentational';

const StyledPopover = withStyles({
  paper: {
    width: '363px',
    padding: '0 20px',
    boxShadow: '0 3px 4px 0 rgba(0, 0, 0, 0.06)',
    border: '1px solid #dddddd',
  },
})(Popover);

const popoverPaperProps = {
  style: { borderRadius: 0 },
};

const anchorOriginProps = {
  vertical: 'bottom',
  horizontal: 'right',
};

const transformOriginProps = {
  vertical: 'top',
  horizontal: 'right',
};

const NoticeBox = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const alarmList = useSelector(({ global }) => global.alarmList);

  const [anchorEl, setAnchorEl] = useState(null);
  const [isOpened, setIsOpened] = useState(false);

  const open = Boolean(anchorEl);
  const id = open ? 'alarm-popover' : undefined;

  const refreshAlarmList = async () => {
    const conditions = {
      start: 0,
      length: 5,
      isChecked: false,
    };
    const { data } = await NotificationService.getNotificationList(conditions);

    if (data) {
      dispatch(setAlarmList(data.list || []));
    }
  };

  const handleOpen = event => {
    setIsOpened(prev => !prev);
  };

  const handleClickAlarm = async ({ id, notificationType, contentId, project }) => {
    try {
      await NotificationService.readAlarm(id);
    } finally {
      refreshAlarmList();

      switch (notificationType) {
        case 'BOARD':
          history.push(`/communication/post/${contentId}`, {
            projectId: project.id,
          });
          break;
        case 'REPORT':
          history.push('/report', { id: contentId, projectId: project.id });
          break;
        default:
          break;
      }
    }
    // setAnchorEl(null);
  };

  const renderAlarmList = (list = []) => (
    <>
      {alarmList.length > 0 ? (
        list.map(alarm => (
          <li className='alarm-item' key={shortid.generate()}>
            <button type='button' onClick={() => handleClickAlarm(alarm)}>
              {alarm.content}
            </button>
            <span>{moment(alarm.createDt).format('YYYY-MM-DD')}</span>
          </li>
        ))
      ) : (
        <li className='alarm-item empty'>
          <p>새로운 알림 내역이 없습니다.</p>
        </li>
      )}
    </>
  );

  return (
    <div className='notice-box-wrap'>
      <button
        type='button'
        aria-describedby={id}
        onClick={handleOpen}
        className={`btn-alarm ${isOpened ? 'active' : ''}`}
      >
        <Maybe test={alarmList.length > 0}>
          <i className='new-marker'>
            <span className='accessible-hidden'>새알림</span>
          </i>
        </Maybe>
        <i className='ico-alarm'>
          <span className='accessible-hidden'>알림보기</span>
        </i>
      </button>
      <div className='alarm-list-wrap'>
        <div className='alarm-list-inner'>
          <ul className='alarm-list'>{renderAlarmList(alarmList)}</ul>
          <Link to='/alarm' className='view-more'>
            더보기
          </Link>
        </div>
      </div>
      {/* <StyledPopover
        className='notice-popover'
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={anchorOriginProps}
        transformOrigin={transformOriginProps}
        PaperProps={popoverPaperProps}
        disableScrollLock={true}
      >
        <ul className='alarm-list'>{renderAlarmList(alarmList)}</ul>

        <div className='view-more'>
          <a href='/alarm' title='알림 더보기' onClick={e => moveToAlarmPage(e)}>
            더보기
          </a>
        </div>
      </StyledPopover> */}
    </div>
  );
};

export default NoticeBox;
