import { createSlice } from '@reduxjs/toolkit';

const projectSlice = createSlice({
  name: 'project',
  initialState: {
    projectList: [],
    defaultProject: {
      id: null,
      code: '',
      name: '',
      detailName: '',
      createDt: '',
      modifyDt: '',
    },
  },
  reducers: {
    setDefaultProject: (state, action) => {
      state.defaultProject = {
        id: action.payload.id,
        code: action.payload.code,
        name: action.payload.name,
        detailName: action.payload.detailName,
        createDt: action.payload.createDt,
        modifyDt: action.payload.modifyDt,
      };
    },
    setProjectList: (state, action) => {
      state.projectList = action.payload;
    },
  },
});

const { actions, reducer } = projectSlice;

export const { setDefaultProject, setProjectList } = actions;

export default reducer;
