import './style.scss';
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Maybe } from '../../presentational';
import bgStep1First from '../../../assets/images/background/progress-bar-step1.png';
import bgStep2First from '../../../assets/images/background/progress-bar-step2-first.png';
import bgStep2Second from '../../../assets/images/background/progress-bar-step2-second.png';
import bgStep3First from '../../../assets/images/background/progress-bar-step3-first.png';
import bgStep3Second from '../../../assets/images/background/progress-bar-step3-second.png';
import bgStep3Third from '../../../assets/images/background/progress-bar-step3-third.png';

const ProgressBarStep = ({ list, total, step }) => {
  const [barStyle, setBarStyle] = useState({
    image: bgStep1First,
    stick: 'diamond',
  });
  const [textPosition, setTextPosition] = useState({
    progress1: '0%',
    progress2: '0%',
    progress3: '0%',
    progress4: '0%',
    progress5: '0%',
  });

  const setProgressStyle = () => {
    switch (total) {
      case 1:
        return setBarStyle({ image: bgStep1First, stick: 'diamond' });

      case 2: {
        if (step === 1) {
          return setBarStyle({ image: bgStep2First, stick: 'diamond' });
        }
        return setBarStyle({ image: bgStep2Second, stick: 'rectangle' });
      }
      case 3: {
        if (step === 1) {
          return setBarStyle({ image: bgStep3First, stick: 'circle' });
        }
        if (step === 2) {
          return setBarStyle({ image: bgStep3Second, stick: 'rectangle' });
        }
        return setBarStyle({ image: bgStep3Third, stick: 'diamond' });
      }
      default: {
        return '';
      }
    }
  };

  const alignByProgressLength = () => {
    switch ((list.projectProgressList || []).length) {
      case 1:
        return setTextPosition({
          progress1: '13.5%',
        });
      case 2: {
        return setTextPosition({
          progress1: '16.75%',
          progress2: '30.75%',
        });
      }
      case 3: {
        return setTextPosition({
          progress1: '2.5%',
          progress2: '23.75%',
          progress3: '45%',
        });
      }
      case 4: {
        return setTextPosition({
          progress1: '6.5%',
          progress2: '18.5%',
          progress3: '30.5%',
          progress4: '42.5%',
        });
      }
      case 5: {
        return setTextPosition({
          progress1: '2.5%',
          progress2: '13.5%',
          progress3: '23.5%',
          progress4: '33.5%',
          progress5: '43.5%',
        });
      }
      default: {
        return '';
      }
    }
  };

  useEffect(() => {
    alignByProgressLength();
    setProgressStyle();
    return () => {
      // cleanup
    };
  }, [list]);

  return (
    <>
      <ul className='progress-container' style={{ width: `${100 / total}%` }}>
        <li>
          <div className='progress-bar-updown'>
            <ul>
              {(list.projectProgressList || [])
                .filter(item => item.beneficiaryProgressId)
                .map(item => (
                  <>
                    <Maybe test={item.progress === 1}>
                      <li>
                        <p
                          className={`bar-upper ${barStyle.stick}`}
                          style={{
                            left: `${textPosition.progress1}`,
                          }}
                        >
                          {item.name}
                        </p>
                      </li>
                    </Maybe>
                    <Maybe test={item.progress === 3}>
                      <li>
                        <p
                          className={`bar-upper ${barStyle.stick}`}
                          style={{
                            left: `${textPosition.progress3}`,
                          }}
                        >
                          {item.name}
                        </p>
                      </li>
                    </Maybe>
                    <Maybe test={item.progress === 5}>
                      <li>
                        <p
                          className={`bar-upper ${barStyle.stick}`}
                          style={{ left: `${textPosition.progress5}` }}
                        >
                          {item.name}
                        </p>
                      </li>
                    </Maybe>
                  </>
                ))}
            </ul>
          </div>
          <div
            className='progress-bar'
            style={{
              backgroundImage: `url(${barStyle.image})`,
            }}
          >
            {list.name}
          </div>
          <div className='progress-bar-updown'>
            <ul>
              {(list.projectProgressList || [])
                .filter(item => item.beneficiaryProgressId)
                .reverse()
                .map(item => (
                  <>
                    <Maybe test={item.progress === 4}>
                      <li>
                        <p
                          className={`bar-under ${barStyle.stick}`}
                          style={{
                            left: `${textPosition.progress4}`,
                          }}
                        >
                          {item.name}
                        </p>
                      </li>
                    </Maybe>
                    <Maybe test={item.progress === 2}>
                      <li>
                        <p
                          className={`bar-under ${barStyle.stick}`}
                          style={{
                            left: `${textPosition.progress2}`,
                          }}
                        >
                          {item.name}
                        </p>
                      </li>
                    </Maybe>
                  </>
                ))}
            </ul>
          </div>
        </li>
      </ul>
    </>
  );
};

ProgressBarStep.propTypes = {
  list: PropTypes.object.isRequired,
  total: PropTypes.number.isRequired,
  step: PropTypes.number.isRequired,
};

export default ProgressBarStep;
