import './style.scss';
import React, { useState, useEffect } from 'react';
import HttpStatus from 'http-status';
import { useHistory, useLocation, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { setAuthenticated } from '../../store/slices/globalSlice';
import { Contents, Navigation, Header, CommentContainer } from '../../components/container';
import { Divider, Layout, Maybe } from '../../components/presentational';
import Constants from '../../constants';
import { ProjectService, CommonService } from '../../services';
import { FileUtil, MomnetUtil, StringUtil } from '../../utils';
import env from '../../environments';
import { setDefaultProject } from '../../store/slices/projectSlice';

const CommunicationDetailPage = () => {
  const dispatch = useDispatch();

  const params = useParams();
  const location = useLocation();
  const history = useHistory();

  const simpleUserInfo = useSelector(({ global }) => global.simpleUserInfo);
  const { defaultProject, projectList } = useSelector(({ project }) => ({
    defaultProject: project.defaultProject,
    projectList: project.projectList,
  }));

  const [postId, setPostId] = useState(Number(params.id));
  const [projectId, setProjectId] = useState(defaultProject.id);
  const [title, setTitle] = useState('');
  const [regInfo, setRegInfo] = useState({ regName: '', regCompany: '', regRank: '' });
  const [createDt, setCreateDt] = useState('');
  const [content, setContent] = useState('');
  const [attachList, setAttachList] = useState([]);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [comments, setComments] = useState([]);

  const getCommunicationPost = async () => {
    try {
      const response = await ProjectService.getCommunicationPost(postId);
      setProjectId(response.project.id);
      setTitle(response.title);
      setRegInfo({
        regName: response.regName,
        regCompany: response.regCompany,
        regRank: response.regRank,
      });
      setCreateDt(response.createDt);
      setContent(response.content);
      setAttachList(response.feedbackBoardAttachList || []);
      setIsAuthenticated(response.regId === simpleUserInfo.username);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      } else if (
        error.error_code &&
        (error.error_code === 'entity_not_found' || error.error_code === 'deleted_content')
      ) {
        alert('없거나 삭제된 게시물입니다.\n목록 페이지로 이동합니다.');
        history.replace('/communication/list');
      }
    }
  };

  const getCommunicationComment = async () => {
    try {
      const { list } = await ProjectService.getCommentListOfCommunication(postId);

      setComments(list);
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };
  const moveToEditPage = async e => {
    e.preventDefault();
    history.push(`/communication/edit/${postId}`);
  };

  const handleDeletePost = async () => {
    const isConfirm = window.confirm(Constants.message.Al_01_07);

    if (isConfirm) {
      try {
        if (attachList.length > 0) {
          const removeAttachList = attachList.map(item => ({
            directory: item.path.substring(0, item.path.lastIndexOf('/')),
            fileName: item.path.split('/').pop(),
          }));

          removeAttachList.map(async item => {
            await CommonService.deleteFile(item);
          });
        }

        const response = await ProjectService.deleteCommunicationPost(postId);
        if (response.success_message === 'process success.') {
          history.push('/communication/list');
        }
      } catch (error) {
        if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
          dispatch(setAuthenticated(false));
        }
      }
    }
  };

  const handleSubmitComment = async (postId, content, parentId) => {
    try {
      const response = await ProjectService.createCommentOfCommunication(postId, content, parentId);

      if (response) {
        getCommunicationComment();
        return true;
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const handleEditComment = async (commentId, newContent, parentId) => {
    if (StringUtil.formatter.noSpace(newContent) === '') {
      window.alert(Constants.message.Al_01_12);
      return;
    }

    try {
      const response = await ProjectService.updateCommentOfCommunication(
        commentId,
        newContent,
        parentId,
      );

      if (response.success_message === 'process success.') {
        getCommunicationComment();
        return true;
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const handleDeleteComment = async (commentId, parentId) => {
    try {
      const response = await ProjectService.deleteCommentOfCommunication(commentId, parentId);

      if (response.success_message === 'process success.') {
        getCommunicationComment();
      }
    } catch (error) {
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        dispatch(setAuthenticated(false));
      }
    }
  };

  const moveToCommunicationListPage = e => {
    e.preventDefault();
    history.push('/communication/list');
  };

  const handleDownload = e => {
    e.preventDefault();
    FileUtil.downloadHandler(e.target.href, e.target.title);
  };

  useEffect(() => {
    if (simpleUserInfo.isAgreeTerms) {
      getCommunicationPost();
      getCommunicationComment();
    } else {
      history.replace('/terms/conditions');
    }
  }, []);

  useEffect(() => {
    if (location.state) {
      if (location.state.projectId && defaultProject.id !== location.state.projectId) {
        const newDefaultProject = (projectList || []).find(p => p.id === location.state.projectId);

        if (newDefaultProject) {
          dispatch(setDefaultProject(newDefaultProject));
        }
      }
    }
  }, [location]);

  useEffect(() => {
    window.scrollTo(0, 0);
    document.title = '커뮤니케이션 상세 | 세상파일 프로젝트 리포트';
  }, []);

  return (
    <Layout
      header={<Header />}
      navigation={<Navigation style={{ marginTop: '80px', top: 0 }} />}
      content={
        <Contents title={title}>
          <div className='post-container relative'>
            <div className='post-desc flex'>
              <p>
                {regInfo.regCompany}&nbsp;
                {regInfo.regName}&nbsp;
                {regInfo.regRank}
              </p>
              <span>{createDt ? MomnetUtil.formatter.date(createDt, 'YYYY-MM-DD') : ''}</span>
            </div>
            <Maybe test={isAuthenticated}>
              <div className='absolute right-0 -top-16'>
                <a
                  href={`/communication/edit/${postId}`}
                  title='게시물 수정'
                  className='gray-edit-outlined'
                  onClick={e => moveToEditPage(e)}
                >
                  수정
                </a>
                <button
                  title='게시물 삭제'
                  className='gray-delete-outlined'
                  onClick={handleDeletePost}
                >
                  삭제
                </button>
              </div>
            </Maybe>
            <Divider />
            <div
              className='post-content'
              dangerouslySetInnerHTML={{
                __html: content,
              }}
            />
            <Maybe test={(attachList || []).length > 0}>
              <ul className='py-10 file-area'>
                {(attachList || []).map(item => (
                  <li key={item.id}>
                    <a
                      href={`${env.API_URL}upload/${item.path}`}
                      title={item.originFileName}
                      className='download-button mb-2'
                      target='_blank'
                      rel='noreferrer'
                      download={item.originFileName}
                      onClick={handleDownload}
                    >
                      {item.originFileName} 다운로드
                    </a>
                  </li>
                ))}
              </ul>
            </Maybe>
            <Divider />
            <div className='text-center pt-10'>
              <a
                href='/communication/list'
                title='목록으로'
                className='orange-button-filled'
                onClick={moveToCommunicationListPage}
              >
                목록으로
              </a>
            </div>
            <CommentContainer
              id={postId}
              comments={comments}
              getComments={getCommunicationComment}
              onSubmitComment={handleSubmitComment}
              onEditComment={handleEditComment}
              onDeleteCommnet={handleDeleteComment}
            />
          </div>
        </Contents>
      }
    />
  );
};

export default CommunicationDetailPage;
