import './style.scss';
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import ArrowForwardIosRoundedIcon from '@material-ui/icons/ArrowForwardIosRounded';
import { Dialog, DialogContent, DialogTitle, DialogActions } from '@material-ui/core';
import ArrowBackIosRoundedIcon from '@material-ui/icons/ArrowBackIosRounded';
import CloseIcon from '@material-ui/icons/Close';
import HttpStatus from 'http-status';
import CommonTable from '../common-table';
import { setAuthenticated } from '../../../store/slices/globalSlice';
import BeneficiaryProgressBar from '../beneficiary-progressbar';
import { MomnetUtil, NumberUtil } from '../../../utils';
import { AuthService, ProjectService } from '../../../services';
import { Maybe } from '../../presentational';

const dialogPaperProps = {
  style: { borderRadius: 0, width: '1200px' },
};

const BeneficiaryDialog = ({ open, onClose, beneficiaryId, column }) => {
  const dispatch = useDispatch();
  const [registrationNumber, setRegistrationNumber] = useState('');
  const [name, setName] = useState('');
  const [beneficiaryInfo, setBeneficiaryInfo] = useState('');
  const [beneficiarySolutionList, setBeneficiarySolutionList] = useState([]);
  const [beneficiarySolutionListTotal, setBeneficiarySolutionListTotal] = useState('');
  const [beneficiaryProgressList, setBeneficiaryProgressList] = useState([]);
  const [id, setId] = useState('');
  const [previousId, setPreviousId] = useState('');
  const [nextId, setNextId] = useState('');
  const [isLoaded, setIsLoaded] = useState(false);

  const beneficiarySolutionListColumn = [
    {
      id: 'supplyDate',
      label: '제공일',
      cellRenderer: row => MomnetUtil.formatter.ymd(row.supplyDate),
    },
    {
      id: 'supplyName',
      label: '제공 항목',
    },
    {
      id: 'amount',
      classNames: 'text-right',
      suffix: '원',
      label: '금액',
      cellRenderer: row => NumberUtil.formatter.commas(row.amount),
    },
  ];

  const beneficiarySolutionListTotalColmun = [
    {
      id: 'supplyDate',
      label: '제공일',
      colspan: '2',
    },
    {
      id: 'amount',
      classNames: 'text-right text-orange',
      suffix: '원',
      label: '금액',
      cellRenderer: row => NumberUtil.formatter.commas(row.amount),
    },
  ];

  const maskName = (name = '') => {
    let maskedName = '*';
    if (!name) {
      maskedName = '-';
    } else if (name.length < 3) {
      maskedName = name.substring(0, 1) + '*';
    } else if (name.length >= 3) {
      maskedName = name.substring(0, 2) + '*';
    }
    return maskedName;
  };

  const getBeneficiaryInfo = async () => {
    try {
      const response = await Promise.all([
        ProjectService.getDetailInfoOfBeneficiary(Number(id)),
        ProjectService.getDetailInfoOfSolutionByBeneficiary(Number(id)),
        ProjectService.getDetailInfoOfProgressByBeneficiary(Number(id)),
      ]);
      const beneficiaryDatils = response[0].data;
      const beneficiarySolutionList = response[1].data.list;
      const beneficiaryProgressList = response[2]
        ? response[2].list.filter(item => item.activeFlag === true)
        : [];
      setBeneficiaryProgressList(beneficiaryProgressList);
      setRegistrationNumber(beneficiaryDatils.registrationNumber);
      setName(beneficiaryDatils.name);
      setPreviousId(beneficiaryDatils.prev ? beneficiaryDatils.prev.id : '');
      setNextId(beneficiaryDatils.next ? beneficiaryDatils.next.id : '');
      setBeneficiaryInfo([beneficiaryDatils]);
      setBeneficiarySolutionList(beneficiarySolutionList);
      setBeneficiarySolutionListTotal([
        {
          supplyDate: '제공총액',
          amount: (beneficiarySolutionList || []).reduce((acc, item) => acc + item.amount, 0),
        },
      ]);

      if (response) {
        setIsLoaded(false);
      }
    } catch (error) {
      setIsLoaded(false);
      if (error.status && error.status === HttpStatus.UNAUTHORIZED) {
        await AuthService.signOut();
        dispatch(setAuthenticated(false));
      }
    }
  };

  const handleClickPrevious = () => {
    if (previousId) {
      setId(previousId);
    }
  };

  const handleClickNext = () => {
    if (nextId) {
      setId(nextId);
    }
  };

  const handleClose = () => {
    onClose();
  };

  useEffect(() => {
    if (beneficiaryId) {
      setId(Number(beneficiaryId));
    }
  }, [beneficiaryId]);

  useEffect(() => {
    if (id) {
      getBeneficiaryInfo();
    }
  }, [id]);

  return (
    <Dialog className='beneficiary-dialog' open={open} maxWidth='xl' PaperProps={dialogPaperProps}>
      <DialogTitle disableTypography className='flex justify-between items-center pb-0'>
        <h5>
          <b>{registrationNumber}</b>&nbsp;&nbsp;&nbsp;
          <span>{maskName(name)} 제공내역</span>
        </h5>
      </DialogTitle>
      <DialogContent tabIndex='0'>
        <div>
          <CommonTable
            title={`${registrationNumber} ${name} 대상자 기본정보`}
            summary={`${registrationNumber} ${name} 대상자 기본정보 성별, 생년 지역 정보`}
            column={column.filter(item => item.id !== 'totalAmount' && item.id !== 'detail')}
            data={beneficiaryInfo || []}
          />
        </div>
        <dl>
          <dt>
            제공내역 <span className='text-orange'>{beneficiarySolutionList.length}</span>건
          </dt>
          <dd>
            <CommonTable
              bodyScroll
              tableClassName='tbl-scroll'
              title={`${registrationNumber} ${name} 대상자 제공내역`}
              summary={`${registrationNumber} ${name} 대상자 제공내역 제공일, 제공항목, 금액 내역`}
              column={beneficiarySolutionListColumn}
              data={beneficiarySolutionList || []}
              totalData={beneficiarySolutionListTotal || []}
              totalDataColnumn={beneficiarySolutionListTotalColmun}
            />
          </dd>
          <Maybe test={beneficiaryProgressList.length > 0}>
            <dt>진행과정</dt>
            <dd>
              <BeneficiaryProgressBar list={beneficiaryProgressList || []} />
            </dd>
          </Maybe>
        </dl>
      </DialogContent>
      <DialogActions>
        <div className='w-full text-center pb-5 px-3 flex justify-between'>
          <button
            type='button'
            title='이전 대상자 보기'
            onClick={handleClickPrevious}
            disabled={!previousId}
          >
            <ArrowBackIosRoundedIcon className='mb-1' fontSize='small' />
            <span>이전</span>
          </button>
          <button
            type='button'
            title='팝업 닫기'
            className='orange-button-filled'
            onClick={handleClose}
          >
            <span>닫기</span>
          </button>
          <button
            type='button'
            title='다음 대상자 보기'
            onClick={handleClickNext}
            disabled={!nextId}
          >
            <span>다음</span>
            <ArrowForwardIosRoundedIcon className='mb-1' fontSize='small' />
          </button>
        </div>
      </DialogActions>
      <button type='button' onClick={handleClose} title='팝업 닫기' className='btn-close'>
        <CloseIcon />
      </button>
    </Dialog>
  );
};

BeneficiaryDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  beneficiaryId: PropTypes.string.isRequired,
  column: PropTypes.array,
};

export default BeneficiaryDialog;
